import usersData from '../data/users.json';
import movesData from '../data/moves.json';
import tasksData from '../data/tasks.json';
import offersData from '../data/offers.json';
import guidesData from '../data/guides.json';
import { User, Move, Task, Offer, CityGuide, Notification, ChatMessage } from '../types';

// Mock API functions with simulated delays
export const mockApiCall = <T>(data: T, delay: number = 500): Promise<T> => {
  return new Promise((resolve) => {
    setTimeout(() => resolve(data), delay);
  });
};

// User functions
export const getCurrentUser = (): Promise<User> => {
  return mockApiCall(usersData[0] as User);
};

export const getUsers = (): Promise<User[]> => {
  return mockApiCall(usersData as User[]);
};

// Move functions
export const getMoves = (): Promise<Move[]> => {
  return mockApiCall(movesData as Move[]);
};

export const getMoveById = (moveId: string): Promise<Move | undefined> => {
  const move = movesData.find(m => m.id === moveId) as Move;
  return mockApiCall(move);
};

export const getMovesByUserId = (userId: string): Promise<Move[]> => {
  const moves = movesData.filter(m => m.userId === userId) as Move[];
  return mockApiCall(moves);
};

// Task functions
export const getTasks = (): Promise<Task[]> => {
  return mockApiCall(tasksData as Task[]);
};

export const getTasksByMoveId = (moveId: string): Promise<Task[]> => {
  const tasks = tasksData.filter(t => t.moveId === moveId) as Task[];
  return mockApiCall(tasks);
};

export const updateTaskStatus = (taskId: string, status: Task['status']): Promise<Task> => {
  const task = tasksData.find(t => t.id === taskId) as Task;
  if (task) {
    task.status = status;
    if (status === 'completed') {
      task.completedDate = new Date().toISOString();
    }
  }
  return mockApiCall(task);
};

// Offer functions
export const getOffers = (): Promise<Offer[]> => {
  return mockApiCall(offersData as Offer[]);
};

export const getOffersByMoveId = (moveId: string): Promise<Offer[]> => {
  const offers = offersData.filter(o => o.moveId === moveId) as Offer[];
  return mockApiCall(offers);
};

export const updateOfferStatus = (offerId: string, status: Offer['status']): Promise<Offer> => {
  const offer = offersData.find(o => o.id === offerId) as Offer;
  if (offer) {
    offer.status = status;
  }
  return mockApiCall(offer);
};

// City Guide functions
export const getCityGuides = (): Promise<CityGuide[]> => {
  return mockApiCall(guidesData as CityGuide[]);
};

export const getCityGuideByCity = (city: string): Promise<CityGuide | undefined> => {
  const guide = guidesData.find(g => g.city.toLowerCase() === city.toLowerCase()) as CityGuide;
  return mockApiCall(guide);
};

