// Utility functions
export const formatCurrency = (amount: number): string => {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD'
  }).format(amount);
};

export const formatDate = (dateString: string): string => {
  return new Date(dateString).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric'
  });
};

export const formatDateTime = (dateString: string): string => {
  return new Date(dateString).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit'
  });
};

export const getStatusColor = (status: string): string => {
  switch (status) {
    case 'completed':
    case 'active':
    case 'accepted':
      return '#388E3C';
    case 'in-progress':
    case 'scheduled':
    case 'pending':
      return '#0B79D0';
    case 'not-started':
    case 'researching':
    case 'available':
      return '#3A4856';
    case 'declined':
    case 'not-needed':
      return '#D32F2F';
    default:
      return '#3A4856';
  }
};

export const getPriorityColor = (priority: string): string => {
  switch (priority) {
    case 'high':
      return '#D32F2F';
    case 'medium':
      return '#F57C00';
    case 'low':
      return '#388E3C';
    default:
      return '#3A4856';
  }
}; 