import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { MessageCircle, X, Send, User, Bot, Minus } from 'lucide-react';
import { getChatMessages } from '../utils/data';
import { ChatMessage, User as UserType } from '../types';

interface SupportChatProps {
  isOpen: boolean;
  onToggle: () => void;
  user: UserType | null;
}

const SupportChat: React.FC<SupportChatProps> = ({ isOpen, onToggle, user }) => {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (isOpen) {
      loadMessages();
    }
  }, [isOpen]);

  const loadMessages = async () => {
    setLoading(true);
    try {
      const chatMessages = await getChatMessages();
      setMessages(chatMessages);
    } catch (error) {
      console.error('Error loading chat messages:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSendMessage = () => {
    if (!newMessage.trim()) return;

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      type: 'user',
      message: newMessage,
      timestamp: new Date().toISOString()
    };

    setMessages(prev => [...prev, userMessage]);
    setNewMessage('');

    // Simulate bot response
    setTimeout(() => {
      const sidekickName = 'Sidekick';
      const initialGreeting = user
        ? `Hi! I'm your Move OS Sidekick. Ready to help you plan your move to Denver! How can I help you today?`
        : `Hi! I'm your Move OS Sidekick. How can I help you with your new life in Austin?`;

      const botMessage: ChatMessage = {
        id: (Date.now() + 1).toString(),
        type: 'agent',
        message: initialGreeting,
        timestamp: new Date().toISOString(),
        agentName: sidekickName
      };
      setMessages(prev => [...prev, botMessage]);
    }, 1000);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  return (
    <>
      {/* Floating Action Button */}
      <motion.button
        onClick={onToggle}
        className="fixed bottom-6 right-6 w-16 h-16 rounded-full bg-emerald-500 border-2 border-white shadow-lg hover:bg-emerald-600 transition-colors duration-200 z-40 flex items-center justify-center"
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.95 }}
      >
        {isOpen ? <X size={28} /> : <MessageCircle size={28} />}
      </motion.button>

      {/* Chat Overlay */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-end justify-end p-6"
            onClick={(e) => {
              if (e.target === e.currentTarget) onToggle();
            }}
          >
            <motion.div
              initial={{ opacity: 0, y: 100, scale: 0.95 }}
              animate={{ opacity: 1, y: 0, scale: 1 }}
              exit={{ opacity: 0, y: 100, scale: 0.95 }}
              className="bg-white rounded-lg shadow-xl w-96 h-[500px] flex flex-col"
            >
              {/* Header */}
              <div className="bg-moveos-primary-dark text-white p-4 rounded-t-lg">
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-2">
                    <MessageCircle size={20} />
                    <h3 className="font-semibold">Move OS Sidekick</h3>
                  </div>
                  <button
                    onClick={onToggle}
                    className="p-2 hover:bg-white hover:bg-opacity-20 rounded transition-colors focus:outline-none focus:ring-2 focus:ring-moveos-primary"
                    title="Minimize chat"
                  >
                    <Minus size={20} className="text-white" />
                  </button>
                </div>
                <p className="text-sm opacity-90 mt-1">
                  We're here to help with your move
                </p>
              </div>

              {/* Messages */}
              <div className="flex-1 overflow-y-auto p-4 space-y-4">
                {loading ? (
                  <div className="flex items-center justify-center h-full">
                    <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-moveos-primary"></div>
                  </div>
                ) : (
                  messages.map((message) => (
                    <div
                      key={message.id}
                      className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                    >
                      <div
                        className={`max-w-xs px-4 py-2 rounded-lg shadow ${
                          message.type === 'user'
                            ? 'bg-moveos-primary text-white'
                            : 'bg-white text-moveos-primary border border-moveos-primary'
                        }`}
                      >
                        <div className="flex items-center space-x-2 mb-1">
                          {message.type === 'agent' ? (
                            <Bot size={14} className="text-moveos-primary" />
                          ) : (
                            <User size={14} className="text-white" />
                          )}
                          {message.agentName && (
                            <span className="text-xs opacity-75">{message.agentName}</span>
                          )}
                        </div>
                        <p className="text-sm">{message.message}</p>
                        <p className="text-xs opacity-75 mt-1">
                          {new Date(message.timestamp).toLocaleTimeString([], {
                            hour: '2-digit',
                            minute: '2-digit'
                          })}
                        </p>
                      </div>
                    </div>
                  ))
                )}
              </div>

              {/* Input */}
              <div className="p-4 border-t border-border">
                <div className="flex space-x-2">
                  <input
                    type="text"
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    onKeyPress={handleKeyPress}
                    placeholder="Type your message..."
                    className="flex-1 px-3 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
                  />
                  <button
                    onClick={handleSendMessage}
                    disabled={!newMessage.trim()}
                    className="bg-moveos-primary-dark text-white hover:bg-moveos-primary px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    <Send size={16} />
                  </button>
                </div>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
};

export default SupportChat; 