import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Check, X, Info, ChevronDown, ChevronUp } from 'lucide-react';
import { Offer } from '../types';
import { formatCurrency } from '../utils/data';

interface BundleOfferCardProps {
  offer: Offer;
  onAccept: () => void;
  onDecline: () => void;
  noCard?: boolean;
}

const BundleOfferCard: React.FC<BundleOfferCardProps> = ({ 
  offer, 
  onAccept, 
  onDecline, 
  noCard = false 
}) => {
  const [showDetails, setShowDetails] = useState(false);
  const [showWhyRecommend, setShowWhyRecommend] = useState(false);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'accepted':
        return 'text-success';
      case 'declined':
        return 'text-error';
      case 'pending':
        return 'text-moveos-primary';
      default:
        return 'text-text-secondary';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'accepted':
        return 'Accepted';
      case 'declined':
        return 'Declined';
      case 'pending':
        return 'Pending';
      case 'available':
        return 'Available';
      default:
        return status;
    }
  };

  const content = (
    <>
      {/* Header */}
      <div className="flex items-start justify-between mb-3">
        <div className="flex-1">
          <div className="flex items-center space-x-2 mb-1">
            <h3 className="text-lg font-heading font-semibold text-text-primary card-title mb-1">{offer.title}</h3>
            <span className="text-xs px-2 py-0.5 bg-green-50 text-success rounded-full font-medium" style={{ fontWeight: 500 }}>
              Save {formatCurrency(offer.savings)}
            </span>
          </div>
          <p className="text-base text-text-secondary card-subtitle mb-1">{offer.subtitle}</p>
          <p className="text-sm text-text-secondary mb-1">
            {formatCurrency(offer.monthlySavings)}/month savings
          </p>
        </div>
        <div className="text-right">
          <span className={`text-xs font-medium px-2 py-0.5 bg-gray-50 text-moveos-primary rounded mb-1 block`} style={{ fontWeight: 500 }}>{getStatusText(offer.status)}</span>
          <p className="text-xs text-text-secondary">Valid until {new Date(offer.validUntil).toLocaleDateString()}</p>
        </div>
      </div>
      {/* Services */}
      <div className="space-y-2 mb-3">
        {Array.isArray(offer.services) && offer.services.map((service, idx) => (
          <div
            key={service.id}
            className={`flex items-center justify-between p-3 bg-gray-50 rounded-lg${idx !== offer.services.length - 1 ? ' border-b border-gray-100' : ''}`}
          >
            <div className="flex items-center space-x-2">
              <div 
                className="w-2.5 h-2.5 rounded-full"
                style={{ backgroundColor: service.brandColor }}
              />
              <div>
                <p className="text-base font-medium text-text-primary leading-tight">{service.provider}</p>
                <p className="text-xs text-text-secondary leading-tight">{service.plan}</p>
              </div>
            </div>
            <div className="text-right">
              <p className="text-base font-semibold text-text-primary leading-tight">{formatCurrency(service.price)}/mo</p>
              <p className="text-xs text-text-secondary line-through leading-tight">{formatCurrency(service.originalPrice)}/mo</p>
            </div>
          </div>
        ))}
      </div>
      {/* Why We Recommend */}
      <div className="mb-3">
        <button
          onClick={() => setShowWhyRecommend(!showWhyRecommend)}
          className="flex items-center space-x-1 text-sm text-moveos-primary hover:text-blue-700 font-medium"
        >
          <Info size={16} />
          <span>Why we recommend this</span>
          {showWhyRecommend ? <ChevronUp size={16} /> : <ChevronDown size={16} />}
        </button>
        {showWhyRecommend && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            exit={{ opacity: 0, height: 0 }}
            className="mt-1 p-3 bg-blue-50 rounded-lg"
          >
            <p className="text-sm text-text-secondary leading-snug">{offer.whyWeRecommend}</p>
          </motion.div>
        )}
      </div>
      {/* Actions */}
      {offer.status === 'pending' || offer.status === 'available' ? (
        <div className="flex space-x-2 mt-2">
          <button
            onClick={onAccept}
            className="flex-1 bg-moveos-primary text-white py-2 px-4 rounded-lg font-medium hover:bg-blue-700 transition-colors duration-200 active:scale-95 flex items-center justify-center space-x-1 text-base"
          >
            <Check size={16} />
            <span>Accept Bundle</span>
          </button>
          <button
            onClick={onDecline}
            className="flex-1 border border-gray-300 text-text-secondary py-2 px-4 rounded-lg font-medium hover:bg-gray-50 transition-colors duration-200 active:scale-95 flex items-center justify-center space-x-1 text-base"
          >
            <X size={16} />
            <span>Decline</span>
          </button>
        </div>
      ) : (
        <div className="text-center">
          <span className={`text-xs font-medium px-2 py-0.5 bg-gray-50 text-moveos-primary rounded`} style={{ fontWeight: 500 }}>{getStatusText(offer.status)}</span>
        </div>
      )}
    </>
  );

  return noCard ? (
    <div>{content}</div>
  ) : (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="card border-l-4 p-5 rounded-xl shadow-card"
      style={{ borderLeftColor: offer.services[0]?.brandColor || '#0B79D0' }}
    >
      {content}
    </motion.div>
  );
};

export default BundleOfferCard; 