import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Check, Eye, EyeOff, Shield, Lock } from 'react-feather';
import { useNavigate } from 'react-router-dom';

interface SignupData {
  email: string;
  password: string;
  phone: string;
  agreeToTerms: boolean;
  subscribeToTips: boolean;
}

export default function MoveOSSignup() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState<any>(null);
  const [showPassword, setShowPassword] = useState(false);
  const [signupData, setSignupData] = useState<SignupData>({
    email: '',
    password: '',
    phone: '',
    agreeToTerms: false,
    subscribeToTips: true
  });
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    const storedData = localStorage.getItem('uspsFormData');
    if (storedData) {
      const parsedData = JSON.parse(storedData);
      setFormData(parsedData);
      
      // Pre-fill email from USPS form data (phone is not collected in USPS form)
      setSignupData(prev => ({
        ...prev,
        email: parsedData.email || ''
      }));
    }
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    
    // Simulate account creation
    await new Promise(resolve => setTimeout(resolve, 2000));
    
    // Store complete user data
    const userData = {
      ...formData,
      name: `${formData.firstName || ''} ${formData.lastName || ''}`.trim(),
      ...signupData,
      createdAt: new Date().toISOString()
    };
    localStorage.setItem('userAccount', JSON.stringify(userData));
    
    // Trigger flow completion event
    const event = new CustomEvent('uspsFlowCompleted', {
      detail: { userData }
    });
    window.dispatchEvent(event);
    
    navigate('/prototype-dashboard');
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    setSignupData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  return (
    <div className="min-h-screen" style={{ backgroundColor: 'var(--neutral-bg)' }}>
      {/* Move OS Header */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-6xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="font-bold text-2xl" style={{ color: 'var(--brand-blue)' }}>
                Move OS
              </div>
              <span className="text-sm font-medium" style={{ color: 'var(--text-secondary)' }}>
                Your Personal Moving Operating System
              </span>
            </div>
            <div className="text-sm" style={{ color: 'var(--text-secondary)' }}>
              Step 2 of 2
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-4xl mx-auto px-6 py-12">
        <div className="grid lg:grid-cols-2 gap-12 items-start">
          {/* Form Column */}
          <motion.div
            initial={{ opacity: 0, x: -30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6 }}
          >
            <div className="card">
              <h1 className="text-3xl font-bold mb-2" style={{ color: 'var(--text-primary)' }}>
                Unlock Your Personalized Move Dashboard
              </h1>
              <p className="text-lg mb-8" style={{ color: 'var(--text-secondary)' }}>
                Everything you need for a smooth move, organized in one place.
              </p>

              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Pre-filled Data Preview */}
                {formData && (
                  <motion.div
                    className="bg-green-50 rounded-lg p-4 border border-green-200"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5, delay: 0.2 }}
                  >
                    <div className="flex items-center mb-3">
                      <Check className="w-5 h-5 text-green-600 mr-2" />
                      <span className="font-semibold text-green-800">From your USPS form</span>
                    </div>
                    <div className="grid sm:grid-cols-2 gap-4 text-sm">
                      <div>
                        <label className="block font-medium text-gray-600 mb-1">Name</label>
                        <div className="bg-white p-2 rounded border" style={{ color: 'var(--text-secondary)' }}>
                          {formData.firstName} {formData.lastName}
                        </div>
                      </div>
                      <div>
                        <label className="block font-medium text-gray-600 mb-1">Move Date</label>
                        <div className="bg-white p-2 rounded border" style={{ color: 'var(--text-secondary)' }}>
                          {new Date(formData.moveDate).toLocaleDateString()}
                        </div>
                      </div>
                      <div className="sm:col-span-2">
                        <label className="block font-medium text-gray-600 mb-1">Moving To</label>
                        <div className="bg-white p-2 rounded border" style={{ color: 'var(--text-secondary)' }}>
                          {formData.newAddress}, {formData.newCity}, {formData.newState} {formData.newZip}
                        </div>
                      </div>
                    </div>
                  </motion.div>
                )}

                {/* Email */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.3 }}
                >
                  <label className="block text-sm font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                    Email Address *
                  </label>
                  <input
                    type="email"
                    name="email"
                    value={signupData.email}
                    onChange={handleInputChange}
                    required
                    className="w-full px-4 py-3 border-2 border-gray-200 rounded-lg transition-all duration-200 focus:outline-none focus:border-blue-500 focus:ring-4 focus:ring-blue-500/10"
                    placeholder="your.email@example.com"
                  />
                  {formData && formData.email && signupData.email === formData.email && (
                    <div className="flex items-center mt-2 text-sm text-green-600">
                      <Check className="w-4 h-4 mr-1" />
                      Pre-filled from your USPS form
                    </div>
                  )}
                </motion.div>

                {/* Password */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.4 }}
                >
                  <label className="block text-sm font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                    Create Password *
                  </label>
                  <div className="relative">
                    <input
                      type={showPassword ? 'text' : 'password'}
                      name="password"
                      value={signupData.password}
                      onChange={handleInputChange}
                      required
                      className="w-full px-4 py-3 pr-12 border-2 border-gray-200 rounded-lg transition-all duration-200 focus:outline-none focus:border-blue-500 focus:ring-4 focus:ring-blue-500/10"
                      placeholder="Enter secure password"
                    />
                    <button
                      type="button"
                      onClick={() => setShowPassword(!showPassword)}
                      className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600"
                    >
                      {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                    </button>
                  </div>
                  {signupData.password && (
                    <div className="mt-2 text-xs space-y-1">
                      <div className="flex items-center space-x-2">
                        <div className={`w-2 h-2 rounded-full ${signupData.password.length >= 8 ? 'bg-green-500' : 'bg-gray-300'}`}></div>
                        <span className={signupData.password.length >= 8 ? 'text-green-600' : 'text-gray-500'}>
                          At least 8 characters
                        </span>
                      </div>
                      {signupData.password.length < 8 && (
                        <div className="text-red-500 text-xs ml-4">
                          Password must be at least 8 characters to continue
                        </div>
                      )}
                    </div>
                  )}
                </motion.div>

                {/* Phone (Optional) */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.5 }}
                >
                  <label className="block text-sm font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                    Phone Number <span className="font-normal text-gray-500">(Optional)</span>
                  </label>
                  <input
                    type="tel"
                    name="phone"
                    value={signupData.phone}
                    onChange={handleInputChange}
                    className="w-full px-4 py-3 border-2 border-gray-200 rounded-lg transition-all duration-200 focus:outline-none focus:border-blue-500 focus:ring-4 focus:ring-blue-500/10"
                    placeholder="(555) 123-4567"
                  />
                  <p className="text-xs mt-1" style={{ color: 'var(--text-secondary)' }}>
                    For urgent move updates and appointment reminders
                  </p>
                </motion.div>

                {/* Checkboxes */}
                <motion.div
                  className="space-y-3"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.6 }}
                >
                  <label className="flex items-start">
                    <input
                      type="checkbox"
                      name="agreeToTerms"
                      checked={signupData.agreeToTerms}
                      onChange={handleInputChange}
                      required
                      className="mt-1 mr-3"
                      style={{ accentColor: 'var(--brand-blue)' }}
                    />
                    <span className="text-sm" style={{ color: 'var(--text-secondary)' }}>
                      I agree to the <a href="#" className="text-blue-600 hover:underline">Terms of Service</a> and <a href="#" className="text-blue-600 hover:underline">Privacy Policy</a>
                    </span>
                  </label>
                  
                  <label className="flex items-start">
                    <input
                      type="checkbox"
                      name="subscribeToTips"
                      checked={signupData.subscribeToTips}
                      onChange={handleInputChange}
                      className="mt-1 mr-3"
                      style={{ accentColor: 'var(--brand-blue)' }}
                    />
                    <span className="text-sm" style={{ color: 'var(--text-secondary)' }}>
                      Send me helpful moving tips and local recommendations (you can unsubscribe anytime)
                    </span>
                  </label>
                </motion.div>

                {/* Validation Summary */}
                <motion.div
                  className="bg-gray-50 rounded-lg p-4 border border-gray-200"
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.65 }}
                >
                  <h4 className="text-sm font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                    To continue, please complete:
                  </h4>
                  <div className="space-y-1 text-xs">
                    <div className={`flex items-center ${signupData.email ? 'text-green-600' : 'text-gray-500'}`}>
                      <div className={`w-2 h-2 rounded-full mr-2 ${signupData.email ? 'bg-green-500' : 'bg-gray-300'}`}></div>
                      Enter a valid email address
                    </div>
                    <div className={`flex items-center ${signupData.password.length >= 8 ? 'text-green-600' : 'text-gray-500'}`}>
                      <div className={`w-2 h-2 rounded-full mr-2 ${signupData.password.length >= 8 ? 'bg-green-500' : 'bg-gray-300'}`}></div>
                      Create a password (at least 8 characters)
                    </div>
                    <div className={`flex items-center ${signupData.agreeToTerms ? 'text-green-600' : 'text-gray-500'}`}>
                      <div className={`w-2 h-2 rounded-full mr-2 ${signupData.agreeToTerms ? 'bg-green-500' : 'bg-gray-300'}`}></div>
                      Agree to Terms of Service and Privacy Policy
                    </div>
                  </div>
                </motion.div>

                {/* Submit Button */}
                <motion.button
                  type="submit"
                  disabled={isLoading || !signupData.agreeToTerms || signupData.password.length < 8 || !signupData.email}
                  className="w-full btn-primary py-4 text-lg font-semibold disabled:opacity-50 disabled:cursor-not-allowed"
                  whileHover={{ scale: isLoading ? 1 : 1.02 }}
                  whileTap={{ scale: isLoading ? 1 : 0.98 }}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.7 }}
                >
                  {isLoading ? (
                    <div className="flex items-center justify-center">
                      <div className="loading-spinner mr-2"></div>
                      Setting up your dashboard...
                    </div>
                  ) : (
                    'Create My Move Dashboard'
                  )}
                </motion.button>
              </form>
            </div>
          </motion.div>

          {/* Value Preview Column */}
          <motion.div
            className="space-y-6"
            initial={{ opacity: 0, x: 30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.3 }}
          >
            <div className="card">
              <h3 className="font-semibold text-lg mb-4" style={{ color: 'var(--text-primary)' }}>
                Your Move Dashboard Will Include:
              </h3>
              <div className="space-y-4">
                {[
                  { icon: Check, title: 'Personalized Task Timeline', desc: 'Step-by-step checklist based on your move date' },
                  { icon: Shield, title: 'Trusted Service Recommendations', desc: 'Vetted providers with transparent pricing' },
                  { icon: Lock, title: 'Secure Document Storage', desc: 'Keep important papers organized and accessible' }
                ].map((item, index) => (
                  <motion.div
                    key={index}
                    className="flex items-start space-x-3"
                    initial={{ opacity: 0, x: 20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.5, delay: 0.5 + index * 0.1 }}
                  >
                    <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center flex-shrink-0 mt-1">
                      <item.icon className="w-4 h-4" style={{ color: 'var(--brand-blue)' }} />
                    </div>
                    <div>
                      <h4 className="font-medium mb-1" style={{ color: 'var(--text-primary)' }}>
                        {item.title}
                      </h4>
                      <p className="text-sm" style={{ color: 'var(--text-secondary)' }}>
                        {item.desc}
                      </p>
                    </div>
                  </motion.div>
                ))}
              </div>
            </div>

            <div className="card">
              <h3 className="font-semibold text-lg mb-4" style={{ color: 'var(--text-primary)' }}>
                Estimated Savings
              </h3>
              <div className="space-y-3">
                <div className="flex justify-between items-center">
                  <span className="text-sm" style={{ color: 'var(--text-secondary)' }}>Time saved on research</span>
                  <span className="font-semibold" style={{ color: 'var(--text-primary)' }}>15+ hours</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm" style={{ color: 'var(--text-secondary)' }}>Average cost savings</span>
                  <span className="font-semibold text-green-600">$480</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm" style={{ color: 'var(--text-secondary)' }}>Stress reduction</span>
                  <span className="font-semibold" style={{ color: 'var(--text-primary)' }}>Priceless</span>
                </div>
              </div>
            </div>

            <div className="card bg-blue-50 border border-blue-200">
              <div className="flex items-center mb-3">
                <Shield className="w-5 h-5 mr-2" style={{ color: 'var(--brand-blue)' }} />
                <span className="font-semibold" style={{ color: 'var(--text-primary)' }}>
                  Your Privacy Matters
                </span>
              </div>
              <p className="text-sm" style={{ color: 'var(--text-secondary)' }}>
                We never sell your information. All recommendations are based on quality and value, not hidden fees.
              </p>
            </div>
          </motion.div>
        </div>
      </main>
    </div>
  );
} 