import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { 
  Wifi, 
  Zap, 
  Calendar, 
  MapPin, 
  CheckCircle, 
  Clock, 
  TrendingUp,
  User,
  Bell,
  Settings,
  LogOut,
  CreditCard,
  Award
} from 'react-feather';
import { useUser } from '../contexts/UserContext';
import { getMovesByUserId, getTasksByMoveId, getOffersByMoveId } from '../utils/data';
import { Move, Task, Offer } from '../types';
import ProgressBar from '../components/ProgressBar';
import TaskList from '../components/TaskList';
import BundleOfferCard from '../components/BundleOfferCard';
import CityGuideCard from '../components/CityGuideCard';
import DashboardCard from '../components/DashboardCard';
import { differenceInDays, differenceInYears, differenceInMonths } from 'date-fns';

const Dashboard: React.FC = () => {
  const { user } = useUser();
  const [currentMove, setCurrentMove] = useState<Move | null>(null);
  const [tasks, setTasks] = useState<Task[]>([]);
  const [offers, setOffers] = useState<Offer[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!user) return;
    
    const loadDashboardData = async () => {
      try {
        const moves = await getMovesByUserId(user.id);
        const activeMove = moves.find(move => move.status === 'in-progress') || moves[0];
        setCurrentMove(activeMove);

        if (activeMove) {
          const [moveTasks, moveOffers] = await Promise.all([
            getTasksByMoveId(activeMove.id),
            getOffersByMoveId(activeMove.id)
          ]);
          setTasks(moveTasks);
          setOffers(moveOffers);
        }
      } catch (error) {
        console.error('Error loading dashboard data:', error);
      } finally {
        setLoading(false);
      }
    };

    loadDashboardData();
  }, [user?.id]);

  if (!user) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-moveos-primary"></div>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-moveos-primary"></div>
      </div>
    );
  }

  if (!currentMove) {
    return (
      <div className="text-center py-12">
        <h2 className="text-2xl font-bold text-text-primary mb-4">No Active Move</h2>
        <p className="text-text-secondary mb-6">Start a new move to see your dashboard</p>
        <button className="btn-primary">Start New Move</button>
      </div>
    );
  }

  const completedTasks = tasks.filter(task => task.status === 'completed').length;
  const progressPercentage = (completedTasks / tasks.length) * 100;

  const isCarlos = user.id === 'user-2';
  const fromCity = isCarlos ? (user.currentLocation?.split(',')[0] || 'Miami') : (user.previousLocation?.split(',')[0] || 'Denver');
  const toCity = isCarlos ? (user.targetLocation?.split(',')[0] || 'Denver') : (user.currentLocation?.split(',')[0] || 'Austin');
  const profileImage = isCarlos ? `${process.env.PUBLIC_URL}/images/carlos.png` : (user.profileImage?.startsWith('/images/') ? `${process.env.PUBLIC_URL}${user.profileImage}` : user.profileImage);
  const moveDate = new Date(user.moveDate);
  const moveMonth = moveDate.toLocaleDateString('en-US', { month: 'long' });
  const moveDay = moveDate.getDate();
  const moveYear = moveDate.getFullYear();
  const today = new Date();
  const daysUntilMove = Math.max(0, Math.ceil((moveDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24)));
  const mainCityImage = isCarlos ? `${process.env.PUBLIC_URL}/images/denver.jpg` : `${process.env.PUBLIC_URL}/images/austin.jpg`;
  const firstName = user.name.split(' ')[0];

  // Calculate days since move for post-move users
  let daysSinceMove = 0;
  let moveAgoString = '';
  if (!isCarlos) {
    const now = new Date();
    const moveDateObj = new Date(currentMove.moveDate);
    daysSinceMove = differenceInDays(now, moveDateObj);
    const years = differenceInYears(now, moveDateObj);
    const months = differenceInMonths(now, moveDateObj) % 12;
    if (years > 0) {
      moveAgoString = `${years} year${years > 1 ? 's' : ''}${months > 0 ? ` ${months} month${months > 1 ? 's' : ''}` : ''}`;
    } else if (months > 0) {
      moveAgoString = `${months} month${months > 1 ? 's' : ''}`;
    } else {
      moveAgoString = `${daysSinceMove} day${daysSinceMove !== 1 ? 's' : ''}`;
    }
  }

  return (
    <div className="space-y-8">
      {/* Header Section */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="flex items-center justify-between"
      >
        <div className="flex items-center space-x-4">
          <img
            src={profileImage}
            alt={user.name}
            className="w-20 h-20 rounded-full object-cover border-4 border-white shadow-lg self-center"
          />
          <div className="flex flex-col justify-center">
            <h2 className="text-3xl font-bold mb-1">
              {isCarlos
                ? `${firstName}, your upcoming move from ${fromCity} to ${toCity}`
                : `${firstName}, welcome to your new home in ${toCity}`}
            </h2>
            <span className="text-xl text-text-secondary block">
              {isCarlos ? `Planned Move Date: ${moveMonth} ${moveDay}, ${moveYear}` : `Move Date: ${moveMonth} ${moveDay}, ${moveYear}`}
            </span>
          </div>
        </div>
      </motion.div>

      {/* Progress Overview Section */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
      >
        <div className="section-title-container mb-6">
          <h2 className="section-title">Move Progress</h2>
          <div className="section-title-accent"></div>
        </div>
        
        <div className="card">
          <div className="flex items-center justify-between mb-6">
            <div className="flex items-center space-x-3">
              <div className="w-12 h-12 bg-gradient-to-br from-moveos-primary to-blue-600 rounded-full flex items-center justify-center">
                <TrendingUp size={24} className="text-white" />
              </div>
              <div>
                <h3 className="text-xl font-semibold text-text-primary">Overall Progress</h3>
                <p className="text-text-secondary">
                  {completedTasks} of {tasks.length} tasks completed
                </p>
              </div>
            </div>
            <div className="text-right">
              <p className="text-3xl font-bold text-moveos-primary">{progressPercentage.toFixed(0)}%</p>
              <p className="text-sm text-text-secondary">Complete</p>
            </div>
          </div>
          
          <ProgressBar progress={progressPercentage} />
          
          {isCarlos ? (
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-8">
              <div className="bg-green-50 rounded-xl p-6 flex flex-col items-center">
                <CheckCircle size={32} className="text-green-500 mb-2" />
                <span className="text-2xl font-bold text-success">{completedTasks}</span>
                <span className="text-base text-text-secondary">Tasks Completed</span>
              </div>
              <div className="bg-blue-50 rounded-xl p-6 flex flex-col items-center">
                <TrendingUp size={32} className="text-blue-500 mb-2" />
                <span className="text-2xl font-bold text-moveos-primary">0</span>
                <span className="text-base text-text-secondary">Planned Services</span>
              </div>
              <div className="bg-orange-50 rounded-xl p-6 flex flex-col items-center">
                <Calendar size={32} className="text-orange-500 mb-2" />
                <span className="text-2xl font-bold text-orange-600">{daysUntilMove}</span>
                <span className="text-base text-text-secondary">Days Until Move</span>
              </div>
            </div>
          ) : (
            <div className="mt-6 grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="text-center p-4 bg-gradient-to-br from-green-50 to-green-100 rounded-lg">
                <CheckCircle size={24} className="text-success mx-auto mb-2" />
                <p className="text-2xl font-bold text-success">{completedTasks}</p>
                <p className="text-base text-text-secondary">Completed</p>
              </div>
              <div className="text-center p-4 bg-gradient-to-br from-orange-50 to-orange-100 rounded-lg">
                <Clock size={24} className="text-orange-500 mx-auto mb-2" />
                <p className="text-2xl font-bold text-orange-600">{tasks.length - completedTasks}</p>
                <p className="text-base text-text-secondary">Remaining</p>
              </div>
              <div className="text-center p-4 bg-gradient-to-br from-blue-50 to-blue-100 rounded-lg">
                <Calendar size={24} className="text-moveos-primary mx-auto mb-2" />
                {isCarlos ? (
                  <>
                    <span className="text-2xl font-bold text-moveos-primary">{daysUntilMove}</span>
                    <p className="text-base text-text-secondary">Days Left</p>
                  </>
                ) : (
                  <>
                    <span className="text-2xl font-bold text-moveos-primary">{daysSinceMove}</span>
                    <p className="text-base text-text-secondary">Days ago</p>
                  </>
                )}
              </div>
            </div>
          )}
        </div>
      </motion.div>

      {/* Service Status Section */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.2 }}
      >
        <div className="section-title-container mb-6">
          <h2 className="section-title">Service Status</h2>
          <div className="section-title-accent"></div>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <DashboardCard
            title="Internet"
            provider={currentMove.services.internet.provider}
            status={currentMove.services.internet.status}
            brand="Allconnect"
            brandColor="#009E8F"
            icon={<Wifi size={24} />}
          />
          <DashboardCard
            title="Energy"
            provider={currentMove.services.energy.provider}
            status={currentMove.services.energy.status}
            brand="Choose Energy"
            brandColor="#0E9E4D"
            icon={<Zap size={24} />}
          />
          <DashboardCard
            title="Travel Rewards Card"
            provider={currentMove.services.travelCard.provider}
            status={currentMove.services.travelCard.status}
            brand="The Points Guy"
            brandColor="#0B79D0"
            icon={<CreditCard size={24} />}
          />
        </div>
      </motion.div>

      {/* Tasks and Offers Section */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Task List */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
        >
          <div className="section-title-container mb-6">
            <h2 className="section-title">Your Tasks</h2>
            <div className="section-title-accent"></div>
          </div>
          <TaskList tasks={tasks} onTaskUpdate={(taskId, status) => {
            setTasks(prev => prev.map(task => 
              task.id === taskId ? { ...task, status } : task
            ));
          }} />
        </motion.div>

        {/* Offers */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
        >
          <div className="section-title-container mb-6">
            <h2 className="section-title">Recommended Offers</h2>
            <div className="section-title-accent"></div>
          </div>
          <div className="space-y-4">
            {offers.map((offer) => (
              <BundleOfferCard
                key={offer.id}
                offer={offer}
                onAccept={() => {
                  setOffers(prev => prev.map(o => 
                    o.id === offer.id ? { ...o, status: 'accepted' } : o
                  ));
                }}
                onDecline={() => {
                  setOffers(prev => prev.map(o => 
                    o.id === offer.id ? { ...o, status: 'declined' } : o
                  ));
                }}
              />
            ))}
          </div>
        </motion.div>
      </div>

      {/* City Guide Section */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.5 }}
      >
        <div className="section-title-container mb-6">
          <h2 className="section-title">Explore Your New City</h2>
          <div className="section-title-accent"></div>
        </div>
        <CityGuideCard city={currentMove.toLocation} user={user} />
      </motion.div>

      {/* Recent Achievements Section */}
      {isCarlos ? (
        <div className="text-text-secondary text-center py-8">No achievements yet  your move to Denver is coming up!</div>
      ) : (
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6 }}
        >
          <div className="section-title-container mb-6">
            <h2 className="section-title">Recent Achievements</h2>
            <div className="section-title-accent"></div>
          </div>
          <div className="card flex items-center space-x-4">
            <div className="w-12 h-12 bg-gradient-to-br from-mint-100 to-mint-200 rounded-full flex items-center justify-center">
              <TrendingUp size={24} className="text-moveos-primary" />
            </div>
            <div className="flex-1">
              <h3 className="font-semibold text-text-primary">Services Bundled</h3>
              <p className="text-text-secondary text-sm">Internet, energy, and security connected</p>
            </div>
            <div className="text-right">
              <p className="text-sm text-text-secondary">March 20, 2024</p>
              <div className="flex items-center space-x-1 mt-1">
                <Award size={16} className="text-yellow-500" />
                <span className="text-sm text-yellow-600 font-medium">Achievement</span>
              </div>
            </div>
          </div>
        </motion.div>
      )}
    </div>
  );
};

export default Dashboard; 