import React, { useState, useEffect, useRef } from 'react';
import { motion } from 'framer-motion';
import { 
  Calendar, 
  MapPin, 
  CheckCircle, 
  Clock, 
  TrendingUp,
  Check,
  ArrowRight,
  Star,
  Users,
  Lock,
  FileText,
  DollarSign,
  X,
  Play,
  Info,
  Wifi,
  Zap,
  CreditCard
} from 'react-feather';
import { useNavigate } from 'react-router-dom';
import ServicePreferencesModal from '../components/ServicePreferencesModal';
import TopBar from '../components/TopBar';
import Sidebar from '../components/Sidebar';
import DashboardCard from '../components/DashboardCard';
import TaskList from '../components/TaskList';
import BundleOfferCard from '../components/BundleOfferCard';
import CityGuideCard from '../components/CityGuideCard';
import USPSCompletionModal from '../components/USPSCompletionModal';
import users from '../data/users.json';

interface PrototypeUser {
  id: string;
  name: string;
  email: string;
  phone?: string;
  currentAddress: string;
  currentCity: string;
  currentState: string;
  currentZip: string;
  newAddress: string;
  newCity: string;
  newState: string;
  newZip: string;
  moveDate: string;
  createdAt: string;
}

interface PrototypeTask {
  id: string;
  title: string;
  description: string;
  status: 'pending' | 'in-progress' | 'completed';
  category: string;
  priority: 'low' | 'medium' | 'high';
  dueDate?: string;
  completedAt?: string;
}

interface PrototypeService {
  name: string;
  status: 'pending' | 'scheduled' | 'completed';
  provider?: string;
  scheduledDate?: string;
  estimatedCost?: number;
  actualCost?: number;
  savings?: number;
}

interface PrototypeOffer {
  id: string;
  title: string;
  description: string;
  provider: string;
  originalPrice: number;
  discountedPrice: number;
  savings: number;
  status: 'available' | 'accepted' | 'declined';
  category: string;
  validUntil: string;
}

const PrototypeDashboard: React.FC = () => {
  const navigate = useNavigate();
  const [userData, setUserData] = useState<PrototypeUser | null>(null);
  const [tasks, setTasks] = useState<PrototypeTask[]>([]);
  const [services, setServices] = useState<PrototypeService[]>([]);
  const [offers, setOffers] = useState<PrototypeOffer[]>([]);
  const [loading, setLoading] = useState(true);
  const [showOnboarding, setShowOnboarding] = useState(true);
  const [showPreferencesModal, setShowPreferencesModal] = useState(false);
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [showUSPSModal, setShowUSPSModal] = useState(false);
  const mainContentRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Always use Carlos's canonical data for USPS demo mode
    const carlos = users.find((u: any) => u.id === 'user-2');
    if (carlos) {
      setUserData({
        id: carlos.id,
        name: carlos.name,
        email: carlos.email,
        phone: carlos.phone || '',
        currentAddress: carlos.currentLocation || '',
        currentCity: 'Miami',
        currentState: 'FL',
        currentZip: '',
        newAddress: carlos.targetLocation || '',
        newCity: 'Denver',
        newState: 'CO',
        newZip: '',
        moveDate: carlos.moveDate,
        createdAt: carlos.createdAt || ''
      });
    }

    const loadPrototypeData = () => {
      try {
        // Generate prototype tasks based on move timeline
        const prototypeTasks: PrototypeTask[] = [
          {
            id: 'task-1',
            title: 'USPS Change of Address',
            description: 'Mail forwarding setup completed',
            status: 'completed',
            category: 'government',
            priority: 'high',
            completedAt: new Date().toISOString()
          },
          {
            id: 'task-2',
            title: 'Research Moving Companies',
            description: 'Get quotes from 3+ movers',
            status: 'in-progress',
            category: 'moving',
            priority: 'high',
            dueDate: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString()
          },
          {
            id: 'task-3',
            title: 'Schedule Internet Installation',
            description: 'Book appointment for new home',
            status: 'pending',
            category: 'utilities',
            priority: 'medium',
            dueDate: new Date(Date.now() + 14 * 24 * 60 * 60 * 1000).toISOString()
          },
          {
            id: 'task-4',
            title: 'Transfer Energy Service',
            description: 'Set up electricity and gas',
            status: 'pending',
            category: 'utilities',
            priority: 'medium',
            dueDate: new Date(Date.now() + 10 * 24 * 60 * 60 * 1000).toISOString()
          },
          {
            id: 'task-5',
            title: 'Update Driver\'s License',
            description: 'Visit DMV for new state license',
            status: 'pending',
            category: 'government',
            priority: 'low',
            dueDate: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString()
          }
        ];

        // Generate prototype services
        const prototypeServices: PrototypeService[] = [
          {
            name: 'Internet & TV',
            status: 'scheduled',
            provider: 'Allconnect',
            scheduledDate: new Date(Date.now() + 14 * 24 * 60 * 60 * 1000).toISOString(),
            estimatedCost: 89,
            savings: 15
          },
          {
            name: 'Energy Service',
            status: 'pending',
            provider: 'Choose Energy',
            estimatedCost: 120,
            savings: 25
          },
          {
            name: 'Travel Rewards Card',
            status: 'pending',
            provider: 'The Points Guy',
            estimatedCost: 0,
            savings: 200
          }
        ];

        // Generate prototype offers
        const prototypeOffers: PrototypeOffer[] = [
          {
            id: 'offer-1',
            title: 'Moving Company Bundle',
            description: 'Full-service moving with packing and unpacking',
            provider: 'Two Men and a Truck',
            originalPrice: 2500,
            discountedPrice: 2100,
            savings: 400,
            status: 'available',
            category: 'moving',
            validUntil: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000).toISOString()
          },
          {
            id: 'offer-2',
            title: 'Internet + TV Package',
            description: 'High-speed internet with 200+ channels',
            provider: 'Allconnect',
            originalPrice: 104,
            discountedPrice: 89,
            savings: 15,
            status: 'available',
            category: 'utilities',
            validUntil: new Date(Date.now() + 14 * 24 * 60 * 60 * 1000).toISOString()
          }
        ];

        setTasks(prototypeTasks);
        setServices(prototypeServices);
        setOffers(prototypeOffers);
      } catch (error) {
        console.error('Error loading prototype data:', error);
      } finally {
        setLoading(false);
      }
    };

    loadPrototypeData();
  }, []);

  // Modal logic: show modal and handle reset events
  useEffect(() => {
    // Show modal on mount if not dismissed
    if (localStorage.getItem('uspsCompletionModalDismissed') !== 'true') {
      setShowUSPSModal(true);
    }

    // Add event listener for reset
    const resetUSPSModal = () => {
      localStorage.removeItem('uspsCompletionModalDismissed');
      setShowUSPSModal(true);
    };

    window.addEventListener('resetUSPSModal', resetUSPSModal);

    return () => {
      window.removeEventListener('resetUSPSModal', resetUSPSModal);
    };
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-moveos-primary"></div>
      </div>
    );
  }

  if (!userData) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-text-primary mb-4">No User Data Found</h2>
          <p className="text-text-secondary mb-6">Please complete the USPS form first</p>
          <button 
            onClick={() => navigate('/usps-form')}
            className="btn-primary"
          >
            Start USPS Form
          </button>
        </div>
      </div>
    );
  }

  // Safe access to userData properties with fallbacks
  const moveDate = userData?.moveDate ? new Date(userData.moveDate) : new Date();
  const today = new Date();
  const daysUntilMove = Math.max(0, Math.ceil((moveDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24)));
  const firstName = userData?.name ? userData.name.split(' ')[0] : 'User';

  // Provide all required User fields for nav components
  const userForNav = userData && {
    ...userData,
    profileImage: `${process.env.PUBLIC_URL}/images/usps-avatar.png`,
    phone: userData.phone || '',
    currentLocation: userData.currentCity ? `${userData.currentCity}, ${userData.currentState}` : '',
    moveStatus: 'in-progress' as const,
    preferences: {
      internet: '',
      energy: '',
      travelCard: '',
      budget: '',
    },
    consentSettings: {
      marketing: false,
      dataSharing: false,
      notifications: true,
      locationServices: false,
    },
  };

  const completedTasks = tasks.filter(task => task.status === 'completed').length;
  const progressPercentage = tasks.length > 0 ? (completedTasks / tasks.length) * 100 : 0;
  const totalSavings = offers.reduce((sum, offer) => sum + offer.savings, 0) + 
                      services.reduce((sum, service) => sum + (service.savings || 0), 0);

  // Map prototype tasks to main Task type for TaskList
  const mappedTasks = tasks.map((t, i) => {
    let status: 'in-progress' | 'completed' | 'scheduled' | 'not-started' =
      t.status === 'pending' ? 'not-started' :
      t.status === 'in-progress' ? 'in-progress' :
      t.status === 'completed' ? 'completed' : 'not-started';
    let priority: 'low' | 'medium' | 'high' =
      t.priority === 'high' ? 'high' : t.priority === 'medium' ? 'medium' : 'low';
    return {
      id: t.id,
      moveId: 'move-prototype',
      title: t.title,
      description: t.description,
      category: 'moving' as const,
      status,
      priority,
      dueDate: t.dueDate || new Date(Date.now() + (i+1)*3*24*60*60*1000).toISOString(),
      completedDate: t.completedAt || null,
      estimatedTime: '1 hour',
      actualTime: null,
    };
  });

  // Map prototype offers to main Offer type for BundleOfferCard
  const mappedOffers = offers.map((o, i) => {
    let status: 'pending' | 'available' | 'accepted' | 'declined' =
      o.status === 'available' ? 'pending' :
      o.status === 'accepted' ? 'accepted' :
      o.status === 'declined' ? 'declined' : 'pending';
    return {
      id: o.id,
      moveId: 'move-prototype',
      type: 'bundle' as const,
      title: o.title,
      subtitle: o.description,
      brand: o.provider,
      savings: o.savings,
      monthlySavings: Math.floor(o.savings / 12),
      validUntil: o.validUntil,
      status,
      services: [
        {
          id: `service-${i}`,
          provider: o.provider,
          brand: o.provider,
          brandColor: i === 0 ? '#009E8F' : '#0B79D0',
          plan: i === 0 ? 'Full-service Move' : 'Internet + TV',
          price: o.discountedPrice,
          originalPrice: o.originalPrice,
          features: ['Feature 1', 'Feature 2'],
          status: 'scheduled',
          installationDate: null,
          activationDate: null,
        }
      ],
      whyWeRecommend: 'Best value for your move. Highly rated by new residents.',
      createdAt: new Date().toISOString(),
    };
  });

  // Handler to map TaskList status values back to prototype status
  const handleTaskListUpdate = (taskId: string, status: 'in-progress' | 'completed' | 'scheduled' | 'not-started') => {
    let protoStatus: 'pending' | 'in-progress' | 'completed' =
      status === 'not-started' ? 'pending' : status === 'scheduled' ? 'pending' : status;
    handleTaskUpdate(taskId, protoStatus);
  };

  const handleTaskUpdate = (taskId: string, status: 'pending' | 'in-progress' | 'completed') => {
    setTasks(prev => prev.map(task => 
      task.id === taskId 
        ? { ...task, status, completedAt: status === 'completed' ? new Date().toISOString() : undefined }
        : task
    ));
  };

  const handleOfferAction = (offerId: string, action: 'accept' | 'decline') => {
    setOffers(prev => prev.map(offer => 
      offer.id === offerId 
        ? { ...offer, status: action === 'accept' ? 'accepted' : 'declined' }
        : offer
    ));
  };

  const handleManageService = (serviceName: string) => {
    setShowPreferencesModal(true);
  };

  const handleSavePreferences = (preferences: string[]) => {
    setShowPreferencesModal(false);
  };

  const handleSkipPreferences = () => {
    setShowPreferencesModal(false);
  };

  const handleCloseUSPSModal = () => {
    setShowUSPSModal(false);
    localStorage.setItem('uspsCompletionModalDismissed', 'true');
    setTimeout(() => {
      mainContentRef.current?.focus();
    }, 100);
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <TopBar user={userForNav} collapsed={sidebarCollapsed} onToggleSidebar={() => setSidebarCollapsed(!sidebarCollapsed)} />
      <Sidebar collapsed={sidebarCollapsed} onToggle={() => setSidebarCollapsed(!sidebarCollapsed)} user={userForNav} />
      <main className="main-content pt-20 pl-0 md:pl-64" ref={mainContentRef} tabIndex={-1}>
        <div className="max-w-6xl mx-auto px-4 space-y-8">
          {/* Main Dashboard Header (Avatar, Name, Move Info) */}
          <div className="flex items-center space-x-6 mb-6 mt-4">
            <img
              src={userForNav?.profileImage || `${process.env.PUBLIC_URL}/images/carlos.png`}
              alt={userForNav?.name}
              className="w-20 h-20 rounded-full object-cover border-4 border-white shadow-lg self-center"
            />
            <div className="flex flex-col justify-center">
              <h2 className="text-3xl font-bold mb-1 text-text-primary">
                {firstName}, your upcoming move from Miami to Denver
              </h2>
              <span className="text-lg text-text-secondary block">
                Planned Move Date: {moveDate.toLocaleDateString('en-US', { month: 'long', day: 'numeric', year: 'numeric' })}
              </span>
            </div>
          </div>

          {/* Your Journey Section */}
          <div className="section-title-container mb-6">
            <h2 className="section-title">Your Journey</h2>
            <div className="section-title-accent"></div>
          </div>
          <div className="card mb-8">
            <div className="space-y-4">
              <div className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                  <Check className="w-5 h-5 text-green-600" />
                </div>
                <div>
                  <h4 className="font-semibold text-text-primary text-lg">USPS Change of Address</h4>
                  <p className="text-sm text-text-secondary">Completed at 2:30 PM</p>
                </div>
              </div>
              <div className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                  <Check className="w-5 h-5 text-green-600" />
                </div>
                <div>
                  <h4 className="font-semibold text-text-primary text-lg">USPS Confirmation</h4>
                  <p className="text-sm text-text-secondary">Completed at 2:33 PM</p>
                </div>
              </div>
              <div className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center">
                  <Check className="w-5 h-5 text-green-600" />
                </div>
                <div>
                  <h4 className="font-semibold text-text-primary text-lg">Move OS Account Creation</h4>
                  <p className="text-sm text-text-secondary">Completed at 2:35 PM</p>
                </div>
              </div>
              <div className="flex items-center space-x-3">
                <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                  <Play className="w-5 h-5 text-blue-600" />
                </div>
                <div>
                  <h4 className="font-semibold text-text-primary text-lg">Personalized Dashboard</h4>
                  <p className="text-sm text-text-secondary">Active - 2:37 PM</p>
                </div>
              </div>
            </div>
          </div>

          {/* Move Progress */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
          >
            <div className="section-title-container mb-6">
              <h2 className="section-title">Move Progress</h2>
              <div className="section-title-accent"></div>
            </div>
            <div className="card">
              <div className="flex items-center justify-between mb-6">
                <div className="flex items-center space-x-3">
                  <div className="w-12 h-12 bg-gradient-to-br from-moveos-primary to-blue-600 rounded-full flex items-center justify-center">
                    <TrendingUp size={24} className="text-white" />
                  </div>
                  <div>
                    <h3 className="text-xl font-semibold text-text-primary">Overall Progress</h3>
                    <p className="text-text-secondary">
                      {completedTasks} of {tasks.length} tasks completed
                    </p>
                  </div>
                </div>
                <div className="text-right">
                  <p className="text-3xl font-bold text-moveos-primary">{progressPercentage.toFixed(0)}%</p>
                  <p className="text-sm text-text-secondary">Complete</p>
                </div>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2 mb-6">
                <div 
                  className="bg-gradient-to-r from-moveos-primary to-blue-600 h-2 rounded-full transition-all duration-500"
                  style={{ width: `${progressPercentage}%` }}
                />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="bg-green-50 rounded-xl p-6 flex flex-col items-center">
                  <CheckCircle size={32} className="text-green-500 mb-2" />
                  <span className="text-2xl font-bold text-success">{completedTasks}</span>
                  <span className="text-base text-text-secondary">Tasks Completed</span>
                </div>
                <div className="bg-blue-50 rounded-xl p-6 flex flex-col items-center">
                  <DollarSign size={32} className="text-blue-500 mb-2" />
                  <span className="text-2xl font-bold text-moveos-primary">${totalSavings}</span>
                  <span className="text-base text-text-secondary">Total Savings</span>
                </div>
                <div className="bg-orange-50 rounded-xl p-6 flex flex-col items-center">
                  <Calendar size={32} className="text-orange-500 mb-2" />
                  <span className="text-2xl font-bold text-orange-600">{daysUntilMove}</span>
                  <span className="text-base text-text-secondary">Days Until Move</span>
                </div>
              </div>
            </div>
          </motion.div>

          {/* Service Status */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
          >
            <div className="section-title-container mb-6">
              <h2 className="section-title">Service Status</h2>
              <div className="section-title-accent"></div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <DashboardCard
                title="Internet"
                provider={services[0]?.provider || 'Allconnect'}
                status={services[0]?.status || 'pending'}
                brand="Allconnect"
                brandColor="#009E8F"
                icon={<Wifi size={24} />}
              />
              <DashboardCard
                title="Energy"
                provider={services[1]?.provider || 'Choose Energy'}
                status={services[1]?.status || 'pending'}
                brand="Choose Energy"
                brandColor="#0E9E4D"
                icon={<Zap size={24} />}
              />
              <DashboardCard
                title="Travel Rewards Card"
                provider={services[2]?.provider || 'The Points Guy'}
                status={services[2]?.status || 'pending'}
                brand="The Points Guy"
                brandColor="#0B79D0"
                icon={<CreditCard size={24} />}
              />
            </div>
          </motion.div>

          {/* Tasks and Offers Section */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* Task List */}
            <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ delay: 0.3 }}>
              <div className="card">
                <div className="text-sm font-medium text-text-primary mb-1">Your Tasks</div>
                <TaskList tasks={mappedTasks} onTaskUpdate={handleTaskListUpdate} noCard={true} />
              </div>
            </motion.div>
            {/* Offers */}
            <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ delay: 0.4 }}>
              <div className="card">
                <div className="text-sm font-medium text-text-primary mb-1">Recommended Offers</div>
                <div className="space-y-4">
                  {mappedOffers.map((offer, i) => (
                    <BundleOfferCard
                      key={offer.id}
                      offer={offer}
                      onAccept={() => handleOfferAction(offer.id, 'accept')}
                      onDecline={() => handleOfferAction(offer.id, 'decline')}
                      noCard={true}
                    />
                  ))}
                </div>
              </div>
            </motion.div>
          </div>

          {/* City Guide */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
          >
            <div className="section-title-container mb-6">
              <h2 className="section-title">Explore Your New City</h2>
              <div className="section-title-accent"></div>
            </div>
            <div className="card">
              <CityGuideCard city={userData?.newCity || 'Denver'} user={userForNav} noCard />
            </div>
          </motion.div>

          {/* Footer/No achievements message */}
          <div className="text-center text-text-secondary text-sm py-8">
            No achievements yet  your move to Denver is coming up!
          </div>
        </div>
        <ServicePreferencesModal
          isOpen={showPreferencesModal}
          onClose={() => setShowPreferencesModal(false)}
          onSave={handleSavePreferences}
          onSkip={handleSkipPreferences}
        />
        <USPSCompletionModal
          isOpen={showUSPSModal}
          onClose={handleCloseUSPSModal}
          onContinue={handleCloseUSPSModal}
        />
      </main>
    </div>
  );
};

export default PrototypeDashboard; 