import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Check, Shield, Lock, ArrowRight, Play, Info, Zap } from 'react-feather';
import { useNavigate } from 'react-router-dom';

interface FormData {
  moveType: string;
  firstName: string;
  lastName: string;
  email: string;
  currentAddress: string;
  currentCity: string;
  currentState: string;
  currentZip: string;
  newAddress: string;
  newCity: string;
  newState: string;
  newZip: string;
  moveDate: string;
}

interface FormErrors {
  [key: string]: string;
}

export default function USPSForm() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState<FormData>({
    moveType: '',
    firstName: '',
    lastName: '',
    email: '',
    currentAddress: '',
    currentCity: '',
    currentState: '',
    currentZip: '',
    newAddress: '',
    newCity: '',
    newState: '',
    newZip: '',
    moveDate: ''
  });
  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [demoMode, setDemoMode] = useState(false);
  const [showDemoNotification, setShowDemoNotification] = useState(false);

  // Demo data for auto-fill
  const demoData: FormData = {
    moveType: 'Family-first',
    firstName: 'Carlos',
    lastName: 'Rodriguez',
    email: 'carlos.rodriguez@email.com',
    currentAddress: 'Miami, FL',
    currentCity: 'Miami',
    currentState: 'FL',
    currentZip: '',
    newAddress: 'Denver, CO',
    newCity: 'Denver',
    newState: 'CO',
    newZip: '',
    moveDate: '2025-08-01'
  };

  // Load existing form data if available
  useEffect(() => {
    const existingData = localStorage.getItem('uspsFormData');
    if (existingData) {
      try {
        const parsed = JSON.parse(existingData);
        setFormData(parsed);
      } catch (error) {
        console.error('Error parsing existing form data:', error);
      }
    }
  }, []);

  // Store timestamp when component mounts
  useEffect(() => {
    if (!localStorage.getItem('uspsFlowTimestamp')) {
      localStorage.setItem('uspsFlowTimestamp', new Date().toISOString());
    }
  }, []);

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.moveType) {
      newErrors.moveType = 'Please select who is moving';
    }

    if (!formData.firstName.trim()) {
      newErrors.firstName = 'First name is required';
    }

    if (!formData.lastName.trim()) {
      newErrors.lastName = 'Last name is required';
    }

    if (!formData.email.trim()) {
      newErrors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Please enter a valid email address';
    }

    if (!formData.currentAddress.trim()) {
      newErrors.currentAddress = 'Current address is required';
    }

    if (!formData.currentCity.trim()) {
      newErrors.currentCity = 'Current city is required';
    }

    if (!formData.currentState.trim()) {
      newErrors.currentState = 'Current state is required';
    }

    if (!formData.currentZip.trim()) {
      newErrors.currentZip = 'Current ZIP code is required';
    } else if (!/^\d{5}(-\d{4})?$/.test(formData.currentZip)) {
      newErrors.currentZip = 'Please enter a valid ZIP code';
    }

    if (!formData.newAddress.trim()) {
      newErrors.newAddress = 'New address is required';
    }

    if (!formData.newCity.trim()) {
      newErrors.newCity = 'New city is required';
    }

    if (!formData.newState.trim()) {
      newErrors.newState = 'New state is required';
    }

    if (!formData.newZip.trim()) {
      newErrors.newZip = 'New ZIP code is required';
    } else if (!/^\d{5}(-\d{4})?$/.test(formData.newZip)) {
      newErrors.newZip = 'Please enter a valid ZIP code';
    }

    if (!formData.moveDate) {
      newErrors.moveDate = 'Move date is required';
    } else {
      const moveDate = new Date(formData.moveDate);
      const today = new Date();
      if (moveDate < today) {
        newErrors.moveDate = 'Move date cannot be in the past';
      }
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);

    // Simulate form processing
    await new Promise(resolve => setTimeout(resolve, 2000));

    // Store form data in localStorage with USPS-specific key
    localStorage.setItem('uspsFormData', JSON.stringify(formData));
    
    // Show success notification
    setShowDemoNotification(true);
    setTimeout(() => {
      setShowDemoNotification(false);
      navigate('/usps-confirmation');
    }, 1500);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    // Clear error when user starts typing
    if (errors[name]) {
      setErrors(prev => ({
        ...prev,
        [name]: ''
      }));
    }
  };

  const handleDemoModeToggle = () => {
    if (demoMode) {
      // Clear form
      setFormData({
        moveType: '',
        firstName: '',
        lastName: '',
        email: '',
        currentAddress: '',
        currentCity: '',
        currentState: '',
        currentZip: '',
        newAddress: '',
        newCity: '',
        newState: '',
        newZip: '',
        moveDate: ''
      });
    } else {
      // Fill with demo data
      setFormData(demoData);
    }
    setDemoMode(!demoMode);
  };

  const getInputClassName = (fieldName: string) => {
    const baseClasses = "w-full px-4 py-3 border-2 rounded-lg transition-all duration-200 focus:outline-none focus:ring-4";
    const errorClasses = errors[fieldName] 
      ? "border-red-300 focus:border-red-500 focus:ring-red-500/10" 
      : "border-gray-200 focus:border-blue-800 focus:ring-blue-800/10";
    return `${baseClasses} ${errorClasses}`;
  };

  return (
    <div className="min-h-screen" style={{ backgroundColor: 'var(--neutral-bg)' }}>
      {/* Demo Mode Notification */}
      {showDemoNotification && (
        <motion.div
          initial={{ opacity: 0, y: -50 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -50 }}
          className="fixed top-4 right-4 bg-green-500 text-white px-6 py-3 rounded-lg shadow-lg z-50"
        >
          <div className="flex items-center space-x-2">
            <Check className="w-5 h-5" />
            <span>Form submitted successfully!</span>
          </div>
        </motion.div>
      )}

      {/* USPS Header */}
      <header className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-6xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="font-bold text-2xl text-blue-800">USPS.COM</div>
              <div className="hidden sm:flex items-center space-x-4 text-sm">
                <span className="flex items-center text-green-600 font-medium">
                  <Shield className="w-4 h-4 mr-1" />
                  Secure
                </span>
                <span className="flex items-center text-green-600 font-medium">
                  <Check className="w-4 h-4 mr-1" />
                  Official
                </span>
                <span className="flex items-center text-green-600 font-medium">
                  <Lock className="w-4 h-4 mr-1" />
                  Encrypted
                </span>
              </div>
            </div>
            <div className="flex items-center space-x-4">
              <div className="text-sm text-gray-600">
                Step 1 of 2
              </div>
              {/* Demo Mode Toggle */}
              <button
                onClick={handleDemoModeToggle}
                className={`flex items-center space-x-2 px-3 py-1 rounded-lg text-sm font-medium transition-all duration-200 ${
                  demoMode 
                    ? 'bg-blue-100 text-blue-800 border border-blue-300' 
                    : 'bg-gray-100 text-gray-600 border border-gray-300 hover:bg-gray-200'
                }`}
              >
                <Zap className="w-4 h-4" />
                <span>{demoMode ? 'Demo Mode ON' : 'Demo Mode'}</span>
              </button>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-5xl mx-auto px-6 py-8">
        {/* Progress Bar */}
        <motion.div 
          className="mb-8"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <div className="flex justify-between text-sm mb-2 text-gray-600">
            <span>Step 1 of 2</span>
            <span>2 minutes remaining</span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-1">
            <motion.div 
              className="bg-blue-800 h-1 rounded-full"
              initial={{ width: 0 }}
              animate={{ width: '50%' }}
              transition={{ duration: 0.8, delay: 0.2 }}
            />
          </div>
        </motion.div>

        <div className="grid lg:grid-cols-3 gap-12">
          {/* Form Column */}
          <motion.div 
            className="lg:col-span-2"
            initial={{ opacity: 0, x: -30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.3 }}
          >
            <div className="card">
              <h1 className="text-3xl font-bold mb-2" style={{ color: 'var(--text-primary)' }}>
                Official USPS Change-of-Address
              </h1>
              <p className="text-lg mb-8" style={{ color: 'var(--text-secondary)' }}>
                Complete your address change in just a few steps
              </p>

              <form onSubmit={handleSubmit} className="space-y-8">
                {/* Who is moving section */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.4 }}
                >
                  <h3 className="text-xl font-semibold mb-4 pb-2 border-b" style={{ 
                    color: 'var(--text-primary)', 
                    borderColor: 'var(--border-color)' 
                  }}>
                    Who is moving?
                  </h3>
                  <div className="grid sm:grid-cols-3 gap-4">
                    {[
                      { value: 'individual', label: 'Individual', icon: '' },
                      { value: 'family', label: 'Family', icon: '' },
                      { value: 'business', label: 'Business', icon: '' }
                    ].map((type) => (
                      <motion.label 
                        key={type.value} 
                        className={`flex flex-col items-center p-6 border-2 rounded-xl cursor-pointer transition-all duration-200 hover:shadow-md ${
                          formData.moveType === type.value 
                            ? 'border-blue-800 bg-blue-50' 
                            : 'border-gray-200 hover:border-blue-300'
                        }`}
                        whileHover={{ scale: 1.02 }}
                        whileTap={{ scale: 0.98 }}
                      >
                        <div className="text-2xl mb-2">{type.icon}</div>
                        <input 
                          type="radio" 
                          name="moveType" 
                          value={type.value}
                          onChange={handleInputChange}
                          className="sr-only"
                        />
                        <span className="font-medium">{type.label}</span>
                      </motion.label>
                    ))}
                  </div>
                  {errors.moveType && (
                    <p className="text-red-600 text-sm mt-2">{errors.moveType}</p>
                  )}
                </motion.div>

                {/* Contact Information */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.5 }}
                >
                  <h3 className="text-xl font-semibold mb-4 pb-2 border-b" style={{ 
                    color: 'var(--text-primary)', 
                    borderColor: 'var(--border-color)' 
                  }}>
                    Contact Information
                  </h3>
                  <div className="grid sm:grid-cols-2 gap-6">
                    <div>
                      <label className="block text-sm font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                        First Name *
                      </label>
                      <input 
                        type="text" 
                        name="firstName"
                        value={formData.firstName}
                        onChange={handleInputChange}
                        className={getInputClassName('firstName')}
                      />
                      {errors.firstName && (
                        <p className="text-red-600 text-sm mt-1">{errors.firstName}</p>
                      )}
                    </div>
                    <div>
                      <label className="block text-sm font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                        Last Name *
                      </label>
                      <input 
                        type="text" 
                        name="lastName"
                        value={formData.lastName}
                        onChange={handleInputChange}
                        className={getInputClassName('lastName')}
                      />
                      {errors.lastName && (
                        <p className="text-red-600 text-sm mt-1">{errors.lastName}</p>
                      )}
                    </div>
                  </div>
                  <div className="mt-6">
                    <label className="block text-sm font-semibold mb-2" style={{ color: 'var(--text-primary)' }}>
                      Email Address *
                    </label>
                    <input 
                      type="email" 
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      className={getInputClassName('email')}
                    />
                    {errors.email && (
                      <p className="text-red-600 text-sm mt-1">{errors.email}</p>
                    )}
                  </div>
                </motion.div>

                {/* Current Address */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.6 }}
                >
                  <h3 className="text-xl font-semibold mb-4 pb-2 border-b" style={{ 
                    color: 'var(--text-primary)', 
                    borderColor: 'var(--border-color)' 
                  }}>
                    Current Address
                  </h3>
                  <div className="space-y-4">
                    <div>
                      <input 
                        type="text" 
                        name="currentAddress"
                        value={formData.currentAddress}
                        onChange={handleInputChange}
                        placeholder="Street Address" 
                        className={getInputClassName('currentAddress')}
                      />
                      {errors.currentAddress && (
                        <p className="text-red-600 text-sm mt-1">{errors.currentAddress}</p>
                      )}
                    </div>
                    <div className="grid sm:grid-cols-3 gap-4">
                      <div>
                        <input 
                          type="text" 
                          name="currentCity"
                          value={formData.currentCity}
                          onChange={handleInputChange}
                          placeholder="City" 
                          className={getInputClassName('currentCity')}
                        />
                        {errors.currentCity && (
                          <p className="text-red-600 text-sm mt-1">{errors.currentCity}</p>
                        )}
                      </div>
                      <div>
                        <input 
                          type="text" 
                          name="currentState"
                          value={formData.currentState}
                          onChange={handleInputChange}
                          placeholder="State" 
                          className={getInputClassName('currentState')}
                        />
                        {errors.currentState && (
                          <p className="text-red-600 text-sm mt-1">{errors.currentState}</p>
                        )}
                      </div>
                      <div>
                        <input 
                          type="text" 
                          name="currentZip"
                          value={formData.currentZip}
                          onChange={handleInputChange}
                          placeholder="ZIP Code" 
                          className={getInputClassName('currentZip')}
                        />
                        {errors.currentZip && (
                          <p className="text-red-600 text-sm mt-1">{errors.currentZip}</p>
                        )}
                      </div>
                    </div>
                  </div>
                </motion.div>

                {/* New Address */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.7 }}
                >
                  <h3 className="text-xl font-semibold mb-4 pb-2 border-b" style={{ 
                    color: 'var(--text-primary)', 
                    borderColor: 'var(--border-color)' 
                  }}>
                    New Address
                  </h3>
                  <div className="space-y-4">
                    <div>
                      <input 
                        type="text" 
                        name="newAddress"
                        value={formData.newAddress}
                        onChange={handleInputChange}
                        placeholder="Street Address" 
                        className={getInputClassName('newAddress')}
                      />
                      {errors.newAddress && (
                        <p className="text-red-600 text-sm mt-1">{errors.newAddress}</p>
                      )}
                    </div>
                    <div className="grid sm:grid-cols-3 gap-4">
                      <div>
                        <input 
                          type="text" 
                          name="newCity"
                          value={formData.newCity}
                          onChange={handleInputChange}
                          placeholder="City" 
                          className={getInputClassName('newCity')}
                        />
                        {errors.newCity && (
                          <p className="text-red-600 text-sm mt-1">{errors.newCity}</p>
                        )}
                      </div>
                      <div>
                        <input 
                          type="text" 
                          name="newState"
                          value={formData.newState}
                          onChange={handleInputChange}
                          placeholder="State" 
                          className={getInputClassName('newState')}
                        />
                        {errors.newState && (
                          <p className="text-red-600 text-sm mt-1">{errors.newState}</p>
                        )}
                      </div>
                      <div>
                        <input 
                          type="text" 
                          name="newZip"
                          value={formData.newZip}
                          onChange={handleInputChange}
                          placeholder="ZIP Code" 
                          className={getInputClassName('newZip')}
                        />
                        {errors.newZip && (
                          <p className="text-red-600 text-sm mt-1">{errors.newZip}</p>
                        )}
                      </div>
                    </div>
                  </div>
                </motion.div>

                {/* Move Date */}
                <motion.div
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.8 }}
                >
                  <h3 className="text-xl font-semibold mb-4 pb-2 border-b" style={{ 
                    color: 'var(--text-primary)', 
                    borderColor: 'var(--border-color)' 
                  }}>
                    Move Date
                  </h3>
                  <input 
                    type="date" 
                    name="moveDate"
                    value={formData.moveDate}
                    onChange={handleInputChange}
                    className={getInputClassName('moveDate')}
                  />
                  {errors.moveDate && (
                    <p className="text-red-600 text-sm mt-1">{errors.moveDate}</p>
                  )}
                </motion.div>

                {/* Submit Button */}
                <motion.button 
                  type="submit" 
                  disabled={isSubmitting}
                  className={`w-full font-semibold py-4 px-6 rounded-lg transition-all duration-200 focus:ring-4 ${
                    isSubmitting 
                      ? 'bg-gray-400 cursor-not-allowed' 
                      : 'bg-blue-800 hover:bg-blue-900 focus:ring-blue-800/20'
                  } text-white`}
                  whileHover={!isSubmitting ? { scale: 1.02 } : {}}
                  whileTap={!isSubmitting ? { scale: 0.98 } : {}}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.5, delay: 0.9 }}
                >
                  {isSubmitting ? (
                    <div className="flex items-center justify-center space-x-2">
                      <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                      <span>Processing...</span>
                    </div>
                  ) : (
                    <div className="flex items-center justify-center space-x-2">
                      <span>Continue to Confirmation</span>
                      <ArrowRight className="w-5 h-5" />
                    </div>
                  )}
                </motion.button>
              </form>
            </div>
          </motion.div>

          {/* Info Sidebar */}
          <motion.div 
            className="space-y-6"
            initial={{ opacity: 0, x: 30 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.6, delay: 0.5 }}
          >
            <div className="card">
              <h3 className="font-semibold text-lg mb-4" style={{ color: 'var(--text-primary)' }}>
                Your Information is Protected
              </h3>
              <div className="space-y-3 text-sm" style={{ color: 'var(--text-secondary)' }}>
                <div className="flex items-center">
                  <Lock className="w-5 h-5 text-green-600 mr-2" />
                  SSL Encrypted
                </div>
                <div className="flex items-center">
                  <Shield className="w-5 h-5 text-green-600 mr-2" />
                  Privacy Protected
                </div>
                <div className="flex items-center">
                  <Check className="w-5 h-5 text-green-600 mr-2" />
                  USPS Verified
                </div>
              </div>
            </div>
            
            <div className="card">
              <h3 className="font-semibold text-lg mb-4" style={{ color: 'var(--text-primary)' }}>
                What Happens Next
              </h3>
              <div className="space-y-4 text-sm" style={{ color: 'var(--text-secondary)' }}>
                <div className="flex items-start">
                  <div className="number-circle mr-3" style={{ 
                    backgroundColor: 'var(--light-blue)', 
                    color: 'var(--brand-blue)',
                    width: '24px',
                    height: '24px',
                    fontSize: '12px'
                  }}>1</div>
                  <span>Confirm your request</span>
                </div>
                <div className="flex items-start">
                  <div className="number-circle mr-3" style={{ 
                    backgroundColor: 'var(--light-blue)', 
                    color: 'var(--brand-blue)',
                    width: '24px',
                    height: '24px',
                    fontSize: '12px'
                  }}>2</div>
                  <span>Mail forwarding begins</span>
                </div>
                <div className="flex items-start">
                  <div className="number-circle mr-3" style={{ 
                    backgroundColor: 'var(--light-blue)', 
                    color: 'var(--brand-blue)',
                    width: '24px',
                    height: '24px',
                    fontSize: '12px'
                  }}>3</div>
                  <span>Receive confirmation</span>
                </div>
              </div>
            </div>
          </motion.div>
        </div>
      </main>
    </div>
  );
} 