import React, { createContext, useContext, useState, useEffect } from 'react';

interface DemoModeContextType {
  demoMode: boolean;
  toggleDemoMode: () => void;
}

const DemoModeContext = createContext<DemoModeContextType | undefined>(undefined);

export const DemoModeProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [demoMode, setDemoMode] = useState<boolean>(() => {
    if (typeof window !== 'undefined') {
      const stored = localStorage.getItem('demoMode');
      if (stored === 'true') return true;
      // If not 'true', clear it
      localStorage.removeItem('demoMode');
      return false;
    }
    return false;
  });

  useEffect(() => {
    localStorage.setItem('demoMode', demoMode ? 'true' : 'false');
  }, [demoMode]);

  const toggleDemoMode = () => setDemoMode((d) => !d);

  return (
    <DemoModeContext.Provider value={{ demoMode, toggleDemoMode }}>
      {children}
    </DemoModeContext.Provider>
  );
};

export function useDemoMode() {
  const ctx = useContext(DemoModeContext);
  if (!ctx) throw new Error('useDemoMode must be used within a DemoModeProvider');
  return ctx;
} 