import React from 'react';
import { motion } from 'framer-motion';
import { getStatusColor } from '../utils/data';

interface DashboardCardProps {
  title: string;
  provider: string | null;
  status: string;
  brand: string;
  brandColor: string;
  icon: React.ReactElement;
}

const DashboardCard: React.FC<DashboardCardProps> = ({
  title,
  provider,
  status,
  brand,
  brandColor,
  icon
}) => {
  const getStatusText = (status: string) => {
    switch (status) {
      case 'active':
        return 'Active';
      case 'scheduled':
        return 'Scheduled';
      case 'pending':
        return 'Pending';
      case 'quoted':
        return 'Quoted';
      case 'researching':
        return 'Researching';
      case 'not-needed':
        return 'Not Needed';
      default:
        return status;
    }
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="card relative overflow-hidden shadow-card rounded-lg transition-shadow duration-200 hover:shadow-card-hover bg-white p-lg"
    >
      {/* Brand accent bar */}
      <div 
        className="absolute top-0 left-0 right-0 h-1"
        style={{ backgroundColor: brandColor }}
      />
      
      {/* Header */}
      <div className="flex items-center justify-between mb-md">
        <div className="flex items-center space-x-3">
          <div 
            className="p-2 rounded-lg"
            style={{ backgroundColor: `${brandColor}20` }}
          >
            <div style={{ color: brandColor }}>
              {icon}
            </div>
          </div>
          <div>
            <h3 className="font-heading text-h3 font-semibold text-text-primary card-title mb-xs">{title}</h3>
            <p className="text-sm text-text-secondary card-subtitle">Powered by {brand}</p>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="space-y-sm">
        <div>
          <p className="text-xs text-text-secondary card-subtitle">Provider</p>
          <p className="font-medium text-text-primary text-body">
            {provider || 'Not selected'}
          </p>
        </div>
        
        <div>
          <p className="text-xs text-text-secondary card-subtitle">Status</p>
          <span 
            className="inline-block px-2 py-1 rounded-full text-xs font-medium"
            style={{ 
              backgroundColor: `${getStatusColor(status)}20`,
              color: getStatusColor(status)
            }}
          >
            {getStatusText(status)}
          </span>
        </div>
      </div>

      {/* Action button */}
      <div className="mt-md">
        <button 
          className="w-full py-2 px-4 rounded-lg border border-border text-text-secondary hover:bg-neutral-bg transition-all duration-200 text-sm font-medium focus:outline-none focus:ring-2 focus:ring-moveos-primary active:scale-95"
          style={{ borderColor: brandColor }}
        >
          {status === 'active' ? 'Manage Service' : 
           status === 'scheduled' ? 'View Details' :
           status === 'pending' ? 'Complete Setup' :
           status === 'quoted' ? 'Review Quote' :
           status === 'researching' ? 'Find Providers' :
           'Get Started'}
        </button>
      </div>
    </motion.div>
  );
};

export default DashboardCard; 