import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { 
  User, 
  Mail, 
  Phone, 
  MapPin, 
  Calendar, 
  Settings as SettingsIcon,
  Bell, 
  Shield,
  Eye,
  EyeOff,
  Save,
  Camera,
  Lock,
  Globe,
  ArrowRight
} from 'react-feather';
import { useUser } from '../contexts/UserContext';

const Settings: React.FC = () => {
  const { user } = useUser();
  const [activeTab, setActiveTab] = useState('profile');
  const [showPassword, setShowPassword] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [settings, setSettings] = useState(() => ({
    profile: {
      name: user?.name || '',
      email: user?.email || '',
      phone: user?.phone || '',
      currentLocation: user?.currentLocation || '',
      previousLocation: user?.previousLocation || ''
    },
    notifications: {
      email: true,
      push: true,
      sms: false,
      marketing: user?.consentSettings?.marketing ?? false,
      moveUpdates: true,
      serviceAlerts: true,
      cityGuide: true,
      offers: true
    },
    privacy: {
      dataSharing: user?.consentSettings?.dataSharing ?? false,
      locationServices: user?.consentSettings?.locationServices ?? false,
      analytics: true,
      personalizedAds: false,
      publicProfile: false
    },
    preferences: {
      theme: 'light',
      language: 'en',
      timezone: 'America/Chicago',
      currency: 'USD',
      units: 'imperial'
    },
    services: {
      internet: user?.preferences?.internet || '',
      energy: user?.preferences?.energy || '',
      budget: user?.preferences?.budget || '',
      travelCard: user?.preferences?.travelCard || ''
    }
  }));

  if (!user) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-moveos-primary"></div>
      </div>
    );
  }

  const tabs = [
    { id: 'profile', name: 'Profile', icon: <User size={20} /> },
    { id: 'notifications', name: 'Notifications', icon: <Bell size={20} /> },
    { id: 'privacy', name: 'Privacy & Security', icon: <Shield size={20} /> },
    { id: 'preferences', name: 'Preferences', icon: <SettingsIcon size={20} /> },
    { id: 'services', name: 'Service Preferences', icon: <Globe size={20} /> }
  ];

  const handleSave = async () => {
    setIsSaving(true);
    // Simulate API call
    setTimeout(() => {
      setIsSaving(false);
    }, 2000);
  };

  const updateSetting = (section: string, key: string, value: any) => {
    setSettings(prev => ({
      ...prev,
      [section]: {
        ...prev[section as keyof typeof prev],
        [key]: value
      }
    }));
  };

  const isCarlos = user.id === 'user-2';
  const fromCity = isCarlos ? (user.currentLocation?.split(',')[0] || 'Miami') : (user.previousLocation?.split(',')[0] || 'Denver');
  const toCity = isCarlos ? (user.targetLocation?.split(',')[0] || 'Denver') : (user.currentLocation?.split(',')[0] || 'Austin');

  const renderProfileTab = () => (
    <div className="space-y-6">
      {/* Profile Photo */}
      <div className="card">
        <h3 className="font-semibold text-text-primary mb-4 flex items-center space-x-2">
          <Camera size={20} className="text-moveos-primary" />
          <span>Profile Photo</span>
        </h3>
        <div className="flex items-center space-x-6">
          <img
            src={`${process.env.PUBLIC_URL}/images/sarah.png`}
            alt={user.name}
            className="w-24 h-24 rounded-full object-cover border-4 border-white shadow-lg"
          />
          <div className="space-y-3">
            <button className="btn-primary flex items-center space-x-2">
              <Camera size={16} />
              <span>Change Photo</span>
            </button>
            <button className="text-text-secondary hover:text-text-primary text-sm">
              Remove Photo
            </button>
          </div>
        </div>
      </div>

      {/* Personal Information */}
      <div className="card">
        <h3 className="font-semibold text-text-primary mb-4 flex items-center space-x-2">
          <User size={20} className="text-moveos-primary" />
          <span>Personal Information</span>
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Full Name
            </label>
            <input
              type="text"
              value={settings.profile.name}
              onChange={(e) => updateSetting('profile', 'name', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Email Address
            </label>
            <input
              type="email"
              value={settings.profile.email}
              onChange={(e) => updateSetting('profile', 'email', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Phone Number
            </label>
            <input
              type="tel"
              value={settings.profile.phone}
              onChange={(e) => updateSetting('profile', 'phone', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Current Location
            </label>
            <input
              type="text"
              value={settings.profile.currentLocation}
              onChange={(e) => updateSetting('profile', 'currentLocation', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            />
          </div>
        </div>
      </div>
    </div>
  );

  const renderNotificationsTab = () => (
    <div className="space-y-6">
      <div className="card">
        <h3 className="font-semibold text-text-primary mb-4 flex items-center space-x-2">
          <Bell size={20} className="text-moveos-primary" />
          <span>Notification Channels</span>
        </h3>
        <div className="space-y-4">
          {Object.entries(settings.notifications).map(([key, value]) => (
            <div key={key} className="flex items-center justify-between py-3 border-b border-border last:border-b-0">
              <div>
                <p className="font-medium text-text-primary capitalize">
                  {key.replace(/([A-Z])/g, ' $1').trim()}
                </p>
                <p className="text-sm text-text-secondary">
                  {key === 'email' && 'Receive notifications via email'}
                  {key === 'push' && 'Receive push notifications on your device'}
                  {key === 'sms' && 'Receive SMS notifications'}
                  {key === 'marketing' && 'Receive marketing and promotional emails'}
                  {key === 'moveUpdates' && 'Get updates about your move progress'}
                  {key === 'serviceAlerts' && 'Get alerts about your services'}
                  {key === 'cityGuide' && 'Get city guide recommendations'}
                  {key === 'offers' && 'Get personalized offers and deals'}
                </p>
              </div>
              <button
                onClick={() => updateSetting('notifications', key, !value)}
                className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                  value ? 'bg-moveos-primary' : 'bg-gray-300'
                }`}
              >
                <span
                  className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                    value ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
            </div>
          ))}
        </div>
      </div>
    </div>
  );

  const renderPrivacyTab = () => (
    <div className="space-y-6">
      <div className="card">
        <h3 className="font-semibold text-text-primary mb-4 flex items-center space-x-2">
          <Shield size={20} className="text-moveos-primary" />
          <span>Privacy Settings</span>
        </h3>
        <div className="space-y-4">
          {Object.entries(settings.privacy).map(([key, value]) => (
            <div key={key} className="flex items-center justify-between py-3 border-b border-border last:border-b-0">
              <div>
                <p className="font-medium text-text-primary capitalize">
                  {key.replace(/([A-Z])/g, ' $1').trim()}
                </p>
                <p className="text-sm text-text-secondary">
                  {key === 'dataSharing' && 'Allow Move OS to share your data with trusted partners'}
                  {key === 'locationServices' && 'Allow Move OS to access your location'}
                  {key === 'analytics' && 'Help us improve by sharing anonymous usage data'}
                  {key === 'personalizedAds' && 'Show personalized advertisements'}
                  {key === 'publicProfile' && 'Make your profile visible to other users'}
                </p>
              </div>
              <button
                onClick={() => updateSetting('privacy', key, !value)}
                className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                  value ? 'bg-moveos-primary' : 'bg-gray-300'
                }`}
              >
                <span
                  className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                    value ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
            </div>
          ))}
        </div>
      </div>

      <div className="card">
        <h3 className="font-semibold text-text-primary mb-4 flex items-center space-x-2">
          <Lock size={20} className="text-moveos-primary" />
          <span>Account Security</span>
        </h3>
        <div className="space-y-4">
          <button className="w-full text-left p-4 border border-border rounded-lg hover:border-moveos-primary hover:bg-moveos-primary/5 transition-all duration-200">
            <div className="flex items-center justify-between">
              <div>
                <p className="font-medium text-text-primary">Change Password</p>
                <p className="text-sm text-text-secondary">Update your account password</p>
              </div>
              <ArrowRight size={16} className="text-text-secondary" />
            </div>
          </button>
          <button className="w-full text-left p-4 border border-border rounded-lg hover:border-moveos-primary hover:bg-moveos-primary/5 transition-all duration-200">
            <div className="flex items-center justify-between">
              <div>
                <p className="font-medium text-text-primary">Two-Factor Authentication</p>
                <p className="text-sm text-text-secondary">Add an extra layer of security</p>
              </div>
              <ArrowRight size={16} className="text-text-secondary" />
            </div>
          </button>
        </div>
      </div>
    </div>
  );

  const renderPreferencesTab = () => (
    <div className="space-y-6">
      <div className="card">
        <h3 className="font-semibold text-text-primary mb-4 flex items-center space-x-2">
          <SettingsIcon size={20} className="text-moveos-primary" />
          <span>App Preferences</span>
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Theme
            </label>
            <select
              value={settings.preferences.theme}
              onChange={(e) => updateSetting('preferences', 'theme', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="light">Light</option>
              <option value="dark">Dark</option>
              <option value="auto">Auto</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Language
            </label>
            <select
              value={settings.preferences.language}
              onChange={(e) => updateSetting('preferences', 'language', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="en">English</option>
              <option value="es">Spanish</option>
              <option value="fr">French</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Timezone
            </label>
            <select
              value={settings.preferences.timezone}
              onChange={(e) => updateSetting('preferences', 'timezone', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="America/Chicago">Central Time</option>
              <option value="America/New_York">Eastern Time</option>
              <option value="America/Denver">Mountain Time</option>
              <option value="America/Los_Angeles">Pacific Time</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Currency
            </label>
            <select
              value={settings.preferences.currency}
              onChange={(e) => updateSetting('preferences', 'currency', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="USD">USD ($)</option>
              <option value="EUR">EUR ()</option>
              <option value="GBP">GBP ()</option>
            </select>
          </div>
        </div>
      </div>
    </div>
  );

  const renderServicesTab = () => (
    <div className="space-y-6">
      <div className="card">
        <h3 className="font-semibold text-text-primary mb-4 flex items-center space-x-2">
          <Globe size={20} className="text-moveos-primary" />
          <span>Service Preferences</span>
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Internet Preference
            </label>
            <select
              value={settings.services.internet}
              onChange={(e) => updateSetting('services', 'internet', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="high-speed">High Speed</option>
              <option value="fiber">Fiber</option>
              <option value="standard">Standard</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Energy Preference
            </label>
            <select
              value={settings.services.energy}
              onChange={(e) => updateSetting('services', 'energy', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="renewable">Renewable</option>
              <option value="standard">Standard</option>
              <option value="premium">Premium</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Travel Card Preference
            </label>
            <select
              value={settings.services.travelCard}
              onChange={(e) => updateSetting('services', 'travelCard', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="bonus-points">Bonus Points</option>
              <option value="lounge-access">Lounge Access</option>
              <option value="none">None</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-text-primary mb-2">
              Budget Preference
            </label>
            <select
              value={settings.services.budget}
              onChange={(e) => updateSetting('services', 'budget', e.target.value)}
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-moveos-primary focus:border-transparent"
            >
              <option value="budget">Budget</option>
              <option value="moderate">Moderate</option>
              <option value="premium">Premium</option>
            </select>
          </div>
        </div>
      </div>
    </div>
  );

  const renderTabContent = () => {
    switch (activeTab) {
      case 'profile':
        return renderProfileTab();
      case 'notifications':
        return renderNotificationsTab();
      case 'privacy':
        return renderPrivacyTab();
      case 'preferences':
        return renderPreferencesTab();
      case 'services':
        return renderServicesTab();
      default:
        return renderProfileTab();
    }
  };

  return (
    <div className="space-y-8">
      {/* Header */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-center"
      >
        <div className="section-title-container mb-6">
          <h1 className="section-title">Settings</h1>
          <div className="section-title-accent"></div>
        </div>
        <p className="text-lg text-text-secondary max-w-3xl mx-auto leading-relaxed">
          Manage your account settings, preferences, and privacy controls.
        </p>
      </motion.div>

      {/* Settings Layout */}
      <div className="flex flex-col md:flex-row gap-8 md:gap-12">
        {/* Vertical Tabs */}
        <nav className="bg-white rounded-2xl shadow-md p-4 flex md:flex-col gap-2 md:gap-1 w-full md:w-64 md:min-h-[420px] items-center md:items-stretch justify-center md:justify-start">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`flex items-center gap-3 px-4 py-2 rounded-xl font-medium transition-all duration-150 text-sm md:text-base focus:outline-none w-full text-left
                ${activeTab === tab.id
                  ? 'bg-moveos-primary text-white shadow-md'
                  : 'bg-transparent text-text-secondary hover:bg-gray-100'}
              `}
              style={{ minWidth: '0', minHeight: '44px' }}
            >
              <span className={`flex items-center justify-center ${activeTab === tab.id ? 'text-white' : 'text-moveos-primary'}`}>{tab.icon}</span>
              <span className="whitespace-nowrap">{tab.name}</span>
            </button>
          ))}
        </nav>

        {/* Tab Content */}
        <div className="flex-1 min-w-0">
          {renderTabContent()}

          {/* Save Button */}
          <div className="mt-8 flex justify-end">
            <button
              onClick={handleSave}
              disabled={isSaving}
              className="btn-primary flex items-center space-x-2"
            >
              {isSaving ? (
                <>
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                  <span>Saving...</span>
                </>
              ) : (
                <>
                  <Save size={20} />
                  <span>Save Changes</span>
                </>
              )}
            </button>
          </div>

          {/* Carlos Summary */}
          {isCarlos && (
            <div className="mb-4 text-moveos-primary font-semibold">Moving from {fromCity} to {toCity}</div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Settings; 