import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { 
  Bell, 
  CheckCircle, 
  AlertCircle, 
  Info, 
  AlertTriangle,
  Clock,
  Star,
  MapPin,
  DollarSign,
  Shield,
  Wifi,
  Zap,
  Heart,
  Trash2,
  Archive,
  Filter,
  Search,
  MoreVertical,
  CreditCard,
  Truck,
  Home
} from 'react-feather';

const Notifications: React.FC = () => {
  const [selectedFilter, setSelectedFilter] = useState('all');
  const [notifications, setNotifications] = useState([
    {
      id: 1,
      type: 'info',
      title: 'Denver City Guide Updated',
      message: 'New recommendations and events have been added to your Denver city guide. Explore neighborhoods and local favorites!',
      timestamp: '1 hour ago',
      read: false,
      icon: <MapPin size={20} />,
      action: 'Explore Guide',
      brand: 'Move OS',
      brandColor: '#0B79D0'
    },
    {
      id: 2,
      type: 'success',
      title: 'Mover Quote Received',
      message: 'You have a new quote from Mile High Movers for your Miami to Denver move.',
      timestamp: '3 hours ago',
      read: false,
      icon: <Truck size={20} />,
      action: 'View Quote',
      brand: 'Mile High Movers',
      brandColor: '#0B79D0'
    },
    {
      id: 3,
      type: 'warning',
      title: 'Lease Start Reminder',
      message: 'Your Denver apartment lease starts in 2 weeks. Confirm your move-in date with your landlord.',
      timestamp: '1 day ago',
      read: true,
      icon: <Home size={20} />,
      action: 'View Details',
      brand: 'Move OS',
      brandColor: '#0B79D0'
    },
    {
      id: 4,
      type: 'info',
      title: 'Travel Card Pre-Approved',
      message: 'You are pre-approved for The Points Guy travel rewards card. Earn points on your move expenses.',
      timestamp: '2 days ago',
      read: true,
      icon: <CreditCard size={20} />,
      action: 'Apply Now',
      brand: 'The Points Guy',
      brandColor: '#0B79D0'
    },
    {
      id: 5,
      type: 'info',
      title: 'Moving Checklist Reminder',
      message: 'Don\'t forget to update your address and set up mail forwarding before your move.',
      timestamp: '3 days ago',
      read: true,
      icon: <CheckCircle size={20} />,
      action: 'View Checklist',
      brand: 'Move OS',
      brandColor: '#0B79D0'
    },
    {
      id: 6,
      type: 'success',
      title: 'Energy Setup Tips',
      message: 'Get the best rates for your new Denver apartment. Compare energy providers and schedule setup.',
      timestamp: '1 week ago',
      read: true,
      icon: <Zap size={20} />,
      action: 'Compare Rates',
      brand: 'Choose Energy',
      brandColor: '#0E9E4D'
    },
    {
      id: 7,
      type: 'info',
      title: 'Internet Setup Guide',
      message: 'Check out our guide to setting up high-speed internet in your new Denver home.',
      timestamp: '1 week ago',
      read: true,
      icon: <Wifi size={20} />,
      action: 'Read Guide',
      brand: 'Allconnect',
      brandColor: '#009E8F'
    }
  ]);

  const filters = [
    { id: 'all', name: 'All', count: notifications.length },
    { id: 'unread', name: 'Unread', count: notifications.filter(n => !n.read).length },
    { id: 'success', name: 'Success', count: notifications.filter(n => n.type === 'success').length },
    { id: 'warning', name: 'Warnings', count: notifications.filter(n => n.type === 'warning').length },
    { id: 'info', name: 'Info', count: notifications.filter(n => n.type === 'info').length }
  ];

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'success':
        return <CheckCircle size={20} className="text-success" />;
      case 'warning':
        return <AlertTriangle size={20} className="text-orange-500" />;
      case 'error':
        return <AlertCircle size={20} className="text-error" />;
      default:
        return <Info size={20} className="text-moveos-primary" />;
    }
  };

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'success':
        return 'border-l-success';
      case 'warning':
        return 'border-l-orange-500';
      case 'error':
        return 'border-l-error';
      default:
        return 'border-l-moveos-primary';
    }
  };

  const markAsRead = (id: number) => {
    setNotifications(prev => 
      prev.map(notification => 
        notification.id === id ? { ...notification, read: true } : notification
      )
    );
  };

  const deleteNotification = (id: number) => {
    setNotifications(prev => prev.filter(notification => notification.id !== id));
  };

  const markAllAsRead = () => {
    setNotifications(prev => 
      prev.map(notification => ({ ...notification, read: true }))
    );
  };

  const filteredNotifications = selectedFilter === 'all' 
    ? notifications 
    : selectedFilter === 'unread'
    ? notifications.filter(n => !n.read)
    : notifications.filter(n => n.type === selectedFilter);

  return (
    <div className="space-y-8">
      {/* Header */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-center"
      >
        <div className="section-title-container mb-6">
          <h1 className="section-title">Notifications</h1>
          <div className="section-title-accent"></div>
        </div>
        <p className="text-lg text-text-secondary max-w-3xl mx-auto leading-relaxed">
          Stay updated with your move progress, service alerts, and personalized recommendations.
        </p>
      </motion.div>

      {/* Stats Overview */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.1 }}
        className="grid grid-cols-1 md:grid-cols-4 gap-6"
      >
        <div className="card text-center">
          <div className="w-12 h-12 bg-gradient-to-br from-moveos-primary to-blue-600 rounded-full flex items-center justify-center mx-auto mb-3">
            <Bell size={24} className="text-white" />
          </div>
          <h3 className="text-xl font-bold text-moveos-primary mb-1">{notifications.length}</h3>
          <p className="text-text-secondary text-sm">Total Notifications</p>
        </div>
        
        <div className="card text-center">
          <div className="w-12 h-12 bg-gradient-to-br from-orange-100 to-orange-200 rounded-full flex items-center justify-center mx-auto mb-3">
            <AlertCircle size={24} className="text-orange-600" />
          </div>
          <h3 className="text-xl font-bold text-orange-600 mb-1">
            {notifications.filter(n => !n.read).length}
          </h3>
          <p className="text-text-secondary text-sm">Unread</p>
        </div>
        
        <div className="card text-center">
          <div className="w-12 h-12 bg-gradient-to-br from-success to-green-600 rounded-full flex items-center justify-center mx-auto mb-3">
            <CheckCircle size={24} className="text-white" />
          </div>
          <h3 className="text-xl font-bold text-success mb-1">
            {notifications.filter(n => n.type === 'success').length}
          </h3>
          <p className="text-text-secondary text-sm">Success</p>
        </div>
        
        <div className="card text-center">
          <div className="w-12 h-12 bg-gradient-to-br from-mint-100 to-mint-200 rounded-full flex items-center justify-center mx-auto mb-3">
            <Clock size={24} className="text-mint-600" />
          </div>
          <h3 className="text-xl font-bold text-mint-600 mb-1">
            {notifications.filter(n => n.type === 'warning').length}
          </h3>
          <p className="text-text-secondary text-sm">Warnings</p>
        </div>
      </motion.div>

      {/* Filters and Actions */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.2 }}
        className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4"
      >
        <div className="flex flex-wrap gap-2">
          {filters.map((filter) => (
            <button
              key={filter.id}
              onClick={() => setSelectedFilter(filter.id)}
              className={`flex items-center space-x-2 px-4 py-2 rounded-full border-2 transition-all duration-200 ${
                selectedFilter === filter.id
                  ? 'border-moveos-primary bg-moveos-primary text-white'
                  : 'border-border text-text-secondary hover:border-moveos-primary hover:text-moveos-primary'
              }`}
            >
              <span className="font-medium">{filter.name}</span>
              <span className="text-xs bg-white/20 px-2 py-1 rounded-full">
                {filter.count}
              </span>
            </button>
          ))}
        </div>
        
        <div className="flex items-center space-x-3">
          <button
            onClick={markAllAsRead}
            className="flex items-center space-x-2 px-4 py-2 text-text-secondary hover:text-text-primary transition-colors"
          >
            <CheckCircle size={16} />
            <span>Mark all as read</span>
          </button>
        </div>
      </motion.div>

      {/* Notifications List */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.3 }}
        className="space-y-4"
      >
        {filteredNotifications.length === 0 ? (
          <div className="card text-center py-12">
            <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <Bell size={32} className="text-gray-400" />
            </div>
            <h3 className="text-xl font-semibold text-text-primary mb-2">No notifications</h3>
            <p className="text-text-secondary">You're all caught up! Check back later for updates.</p>
          </div>
        ) : (
          filteredNotifications.map((notification, index) => (
            <motion.div
              key={notification.id}
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.1 * index }}
              className={`card border-l-4 ${getTypeColor(notification.type)} ${
                !notification.read ? 'bg-blue-50/50' : ''
              }`}
            >
              <div className="flex items-start space-x-4">
                <div className="flex-shrink-0 mt-1">
                  {getTypeIcon(notification.type)}
                </div>
                
                <div className="flex-1 min-w-0">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <div className="flex items-center space-x-2 mb-1">
                        <h3 className="font-semibold text-text-primary">{notification.title}</h3>
                        {!notification.read && (
                          <div className="w-2 h-2 bg-moveos-primary rounded-full"></div>
                        )}
                      </div>
                      <p className="text-text-secondary text-sm mb-2">{notification.message}</p>
                      
                      <div className="flex items-center justify-between">
                        <div className="flex items-center space-x-4 text-xs text-text-secondary">
                          <span className="flex items-center space-x-1">
                            <Clock size={12} />
                            <span>{notification.timestamp}</span>
                          </span>
                          {notification.brand && (
                            <span className="px-2 py-1 rounded-full text-xs font-medium"
                                  style={{ 
                                    backgroundColor: `${notification.brandColor}20`,
                                    color: notification.brandColor 
                                  }}>
                              {notification.brand}
                            </span>
                          )}
                        </div>
                        
                        <div className="flex items-center space-x-2">
                          <button
                            onClick={() => markAsRead(notification.id)}
                            className="text-text-secondary hover:text-text-primary transition-colors px-3 py-1 rounded-full hover:bg-gray-100 hover:border hover:border-gray-200 focus:outline-none focus:ring-2 focus:ring-moveos-primary border border-transparent"
                          >
                            {notification.read ? 'Read' : 'Mark as read'}
                          </button>
                          <button
                            onClick={() => deleteNotification(notification.id)}
                            className="ml-2 flex items-center justify-center w-8 h-8 rounded-full text-text-secondary hover:text-white hover:bg-error hover:border hover:border-gray-200 transition-colors focus:outline-none focus:ring-2 focus:ring-error border border-transparent"
                            aria-label="Delete notification"
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                  
                  {notification.action && (
                    <div className="mt-3">
                      <button
                        className="text-moveos-primary hover:text-blue-700 font-medium text-sm transition-colors px-3 py-1 rounded-full hover:bg-blue-50 hover:border hover:border-blue-200 focus:outline-none focus:ring-2 focus:ring-moveos-primary border border-transparent"
                        style={{ color: notification.brandColor }}
                      >
                        {notification.action} 
                      </button>
                    </div>
                  )}
                </div>
              </div>
            </motion.div>
          ))
        )}
      </motion.div>

      {/* Call to Action */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 0.4 }}
        className="card text-center bg-white border border-moveos-primary/20 shadow rounded-2xl flex flex-col items-center justify-center py-7 px-8 max-w-md mx-auto"
      >
        <div className="w-12 h-12 bg-gradient-to-br from-moveos-primary/90 to-blue-400/80 rounded-full flex items-center justify-center mb-3 shadow-sm">
          <Bell size={28} className="text-white" />
        </div>
        <h3 className="text-xl font-bold mb-2 text-text-primary">Stay in the Loop</h3>
        <p className="text-text-secondary mb-5 text-base">
          Get real-time updates about your move progress, service status, and personalized recommendations.
        </p>
        <button className="w-full max-w-xs mx-auto px-8 py-3 rounded-full bg-moveos-primary text-white font-semibold text-base shadow hover:bg-blue-700 transition-all whitespace-nowrap">
          Manage Notification Settings
        </button>
      </motion.div>
    </div>
  );
};

export default Notifications; 