import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { User as UserType } from '../types';
import { getUsers } from '../utils/data';

interface UserContextType {
  user: UserType | null;
  setUserById: (userId: string) => Promise<void>;
  currentUserId: string | null;
}

const UserContext = createContext<UserContextType | undefined>(undefined);

export const UserProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<UserType | null>(null);
  const [currentUserId, setCurrentUserId] = useState<string | null>(null);

  useEffect(() => {
    // Only restore user if they were previously authenticated
    const storedId = localStorage.getItem('currentUserId');
    if (storedId) {
      setCurrentUserId(storedId);
      getUsers().then(users => {
        const found = users.find(u => u.id === storedId);
        if (found) setUser(found);
      });
    }
  }, []);

  const setUserById = async (userId: string) => {
    setCurrentUserId(userId);
    localStorage.setItem('currentUserId', userId);
    const users = await getUsers();
    const found = users.find(u => u.id === userId);
    if (found) setUser(found);
  };

  return (
    <UserContext.Provider value={{ user, setUserById, currentUserId }}>
      {children}
    </UserContext.Provider>
  );
};

export const useUser = () => {
  const ctx = useContext(UserContext);
  if (!ctx) throw new Error('useUser must be used within a UserProvider');
  return ctx;
}; 