import React, { createContext, useContext, useReducer, useEffect, ReactNode } from 'react';

// Types
export type FlowType = 'sarah' | 'carlos' | 'usps';
export type FlowStatus = 'idle' | 'active' | 'completed' | 'error';

interface FlowProgress {
  currentStep: number;
  totalSteps: number;
  formCompleted: boolean;
  confirmationCompleted: boolean;
  dashboardCompleted: boolean;
  timestamp: string;
}

interface PrototypeFlowState {
  currentFlow: FlowType;
  flowStatus: FlowStatus;
  uspsProgress: FlowProgress | null;
  previousFlow: FlowType | null;
  demoMode: boolean;
  fastDemoMode: boolean;
  showTooltips: boolean;
  lastActivity: string;
  error: string | null;
}

type PrototypeFlowAction =
  | { type: 'SET_CURRENT_FLOW'; payload: FlowType }
  | { type: 'SET_FLOW_STATUS'; payload: FlowStatus }
  | { type: 'UPDATE_USPS_PROGRESS'; payload: Partial<FlowProgress> | null }
  | { type: 'SET_DEMO_MODE'; payload: boolean }
  | { type: 'SET_FAST_DEMO_MODE'; payload: boolean }
  | { type: 'TOGGLE_TOOLTIPS' }
  | { type: 'SET_ERROR'; payload: string | null }
  | { type: 'RESET_FLOW' }
  | { type: 'COMPLETE_FLOW' }
  | { type: 'UPDATE_ACTIVITY' };

// Initial state
const initialState: PrototypeFlowState = {
  currentFlow: 'sarah',
  flowStatus: 'idle',
  uspsProgress: null,
  previousFlow: null,
  demoMode: false,
  fastDemoMode: false,
  showTooltips: true,
  lastActivity: new Date().toISOString(),
  error: null,
};

// Reducer
function prototypeFlowReducer(state: PrototypeFlowState, action: PrototypeFlowAction): PrototypeFlowState {
  switch (action.type) {
    case 'SET_CURRENT_FLOW':
      return {
        ...state,
        currentFlow: action.payload,
        previousFlow: state.currentFlow,
        lastActivity: new Date().toISOString(),
      };
    
    case 'SET_FLOW_STATUS':
      return {
        ...state,
        flowStatus: action.payload,
        lastActivity: new Date().toISOString(),
      };
    
    case 'UPDATE_USPS_PROGRESS':
      return {
        ...state,
        uspsProgress: action.payload === null ? null : {
          ...state.uspsProgress,
          ...action.payload,
          timestamp: new Date().toISOString(),
        } as FlowProgress,
        lastActivity: new Date().toISOString(),
      };
    
    case 'SET_DEMO_MODE':
      return {
        ...state,
        demoMode: action.payload,
        lastActivity: new Date().toISOString(),
      };
    
    case 'SET_FAST_DEMO_MODE':
      return {
        ...state,
        fastDemoMode: action.payload,
        lastActivity: new Date().toISOString(),
      };
    
    case 'TOGGLE_TOOLTIPS':
      return {
        ...state,
        showTooltips: !state.showTooltips,
        lastActivity: new Date().toISOString(),
      };
    
    case 'SET_ERROR':
      return {
        ...state,
        error: action.payload,
        flowStatus: action.payload ? 'error' : state.flowStatus,
        lastActivity: new Date().toISOString(),
      };
    
    case 'RESET_FLOW':
      return {
        ...initialState,
        currentFlow: state.currentFlow,
        lastActivity: new Date().toISOString(),
      };
    
    case 'COMPLETE_FLOW':
      return {
        ...state,
        flowStatus: 'completed',
        lastActivity: new Date().toISOString(),
      };
    
    case 'UPDATE_ACTIVITY':
      return {
        ...state,
        lastActivity: new Date().toISOString(),
      };
    
    default:
      return state;
  }
}

// Context
interface PrototypeFlowContextType {
  state: PrototypeFlowState;
  dispatch: React.Dispatch<PrototypeFlowAction>;
  // Flow control functions
  startUSPSFlow: () => void;
  exitCurrentFlow: () => void;
  resetFlowData: () => void;
  getFlowStatus: () => any;
  // Demo control functions
  toggleDemoMode: () => void;
  toggleFastDemoMode: () => void;
  toggleTooltips: () => void;
  // Progress tracking
  updateUSPSProgress: (progress: Partial<FlowProgress> | null) => void;
  completeUSPSStep: (step: number) => void;
  // Error handling
  setError: (error: string | null) => void;
  clearError: () => void;
}

const PrototypeFlowContext = createContext<PrototypeFlowContextType | undefined>(undefined);

// Provider component
interface PrototypeFlowProviderProps {
  children: ReactNode;
}

export const PrototypeFlowProvider: React.FC<PrototypeFlowProviderProps> = ({ children }) => {
  const [state, dispatch] = useReducer(prototypeFlowReducer, initialState);

  // Initialize state from localStorage
  useEffect(() => {
    const initializeState = () => {
      try {
        const storedFlow = localStorage.getItem('currentPrototypeFlow') as FlowType;
        const uspsFormData = localStorage.getItem('uspsFormData');
        const uspsConfirmationData = localStorage.getItem('uspsConfirmationData');
        const userAccount = localStorage.getItem('userAccount');
        const uspsFlowTimestamp = localStorage.getItem('uspsFlowTimestamp');

        if (storedFlow) {
          dispatch({ type: 'SET_CURRENT_FLOW', payload: storedFlow });
        }

        if (storedFlow === 'usps') {
          const progress: FlowProgress = {
            currentStep: userAccount ? 4 : uspsConfirmationData ? 3 : uspsFormData ? 2 : 1,
            totalSteps: 4,
            formCompleted: !!uspsFormData,
            confirmationCompleted: !!uspsConfirmationData,
            dashboardCompleted: !!userAccount,
            timestamp: uspsFlowTimestamp || new Date().toISOString(),
          };
          
          dispatch({ type: 'UPDATE_USPS_PROGRESS', payload: progress });
          dispatch({ type: 'SET_FLOW_STATUS', payload: userAccount ? 'completed' : 'active' });
        }
      } catch (error) {
        console.error('Error initializing prototype flow state:', error);
        dispatch({ type: 'SET_ERROR', payload: 'Failed to initialize flow state' });
      }
    };

    initializeState();
  }, []);

  // Persist state changes to localStorage
  useEffect(() => {
    localStorage.setItem('currentPrototypeFlow', state.currentFlow);
    
    if (state.currentFlow === 'usps' && state.uspsProgress) {
      localStorage.setItem('uspsFlowTimestamp', state.uspsProgress.timestamp);
    }
  }, [state.currentFlow, state.uspsProgress]);

  // Flow control functions
  const startUSPSFlow = () => {
    try {
      // Store previous flow
      localStorage.setItem('previousPrototypeFlow', state.currentFlow);
      
      // Clear existing USPS data
      localStorage.removeItem('userAccount');
      localStorage.removeItem('uspsFormData');
      localStorage.removeItem('uspsConfirmationData');
      
      // Initialize USPS progress
      const progress: FlowProgress = {
        currentStep: 1,
        totalSteps: 4,
        formCompleted: false,
        confirmationCompleted: false,
        dashboardCompleted: false,
        timestamp: new Date().toISOString(),
      };
      
      dispatch({ type: 'UPDATE_USPS_PROGRESS', payload: progress });
      dispatch({ type: 'SET_CURRENT_FLOW', payload: 'usps' });
      dispatch({ type: 'SET_FLOW_STATUS', payload: 'active' });
      dispatch({ type: 'SET_ERROR', payload: null });
      
      // Navigate to USPS form
      window.location.href = '/usps-form';
    } catch (error) {
      dispatch({ type: 'SET_ERROR', payload: 'Failed to start USPS flow' });
    }
  };

  const exitCurrentFlow = () => {
    try {
      if (state.currentFlow === 'usps') {
        // Clear USPS data
        localStorage.removeItem('userAccount');
        localStorage.removeItem('uspsFormData');
        localStorage.removeItem('uspsConfirmationData');
        localStorage.removeItem('uspsFlowTimestamp');
      }
      
      // Return to default flow (Sarah)
      const defaultFlow: FlowType = 'sarah';
      dispatch({ type: 'SET_CURRENT_FLOW', payload: defaultFlow });
      dispatch({ type: 'SET_FLOW_STATUS', payload: 'idle' });
      dispatch({ type: 'UPDATE_USPS_PROGRESS', payload: null });
      dispatch({ type: 'SET_ERROR', payload: null });
      
      // Navigate to dashboard
      window.location.href = '/dashboard';
    } catch (error) {
      dispatch({ type: 'SET_ERROR', payload: 'Failed to exit current flow' });
    }
  };

  const resetFlowData = () => {
    try {
      // Clear all flow data
      localStorage.removeItem('userAccount');
      localStorage.removeItem('uspsFormData');
      localStorage.removeItem('uspsConfirmationData');
      localStorage.removeItem('uspsFlowTimestamp');
      localStorage.removeItem('previousPrototypeFlow');
      localStorage.removeItem('currentPrototypeFlow');
      // Dispatch event to reset USPS modal
      window.dispatchEvent(new CustomEvent('resetUSPSModal'));
      // Reset state
      dispatch({ type: 'RESET_FLOW' });
      // Reload page
      window.location.reload();
    } catch (error) {
      dispatch({ type: 'SET_ERROR', payload: 'Failed to reset flow data' });
    }
  };

  const getFlowStatus = () => {
    return {
      currentFlow: state.currentFlow,
      flowStatus: state.flowStatus,
      uspsProgress: state.uspsProgress,
      previousFlow: state.previousFlow,
      demoMode: state.demoMode,
      fastDemoMode: state.fastDemoMode,
      lastActivity: state.lastActivity,
      error: state.error,
    };
  };

  // Demo control functions
  const toggleDemoMode = () => {
    dispatch({ type: 'SET_DEMO_MODE', payload: !state.demoMode });
  };

  const toggleFastDemoMode = () => {
    dispatch({ type: 'SET_FAST_DEMO_MODE', payload: !state.fastDemoMode });
  };

  const toggleTooltips = () => {
    dispatch({ type: 'TOGGLE_TOOLTIPS' });
  };

  // Progress tracking
  const updateUSPSProgress = (progress: Partial<FlowProgress> | null) => {
    dispatch({ type: 'UPDATE_USPS_PROGRESS', payload: progress });
  };

  const completeUSPSStep = (step: number) => {
    if (state.uspsProgress) {
      const updatedProgress = { ...state.uspsProgress, currentStep: step };
      
      // Mark steps as completed
      if (step >= 2) updatedProgress.formCompleted = true;
      if (step >= 3) updatedProgress.confirmationCompleted = true;
      if (step >= 4) updatedProgress.dashboardCompleted = true;
      
      dispatch({ type: 'UPDATE_USPS_PROGRESS', payload: updatedProgress });
      
      if (step === 4) {
        dispatch({ type: 'COMPLETE_FLOW' });
      }
    }
  };

  // Error handling
  const setError = (error: string | null) => {
    dispatch({ type: 'SET_ERROR', payload: error });
  };

  const clearError = () => {
    dispatch({ type: 'SET_ERROR', payload: null });
  };

  const contextValue: PrototypeFlowContextType = {
    state,
    dispatch,
    startUSPSFlow,
    exitCurrentFlow,
    resetFlowData,
    getFlowStatus,
    toggleDemoMode,
    toggleFastDemoMode,
    toggleTooltips,
    updateUSPSProgress,
    completeUSPSStep,
    setError,
    clearError,
  };

  return (
    <PrototypeFlowContext.Provider value={contextValue}>
      {children}
    </PrototypeFlowContext.Provider>
  );
};

// Hook to use the context
export const usePrototypeFlow = (): PrototypeFlowContextType => {
  const context = useContext(PrototypeFlowContext);
  if (context === undefined) {
    throw new Error('usePrototypeFlow must be used within a PrototypeFlowProvider');
  }
  return context;
}; 