import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Bell, Settings, User, LogOut, ChevronRight, ChevronLeft } from 'lucide-react';
import { Link, useNavigate } from 'react-router-dom';
import { User as UserType } from '../types';
import { Zap } from 'react-feather';
import { useDemoMode } from '../contexts/DemoModeContext';

interface TopBarProps {
  user: UserType | null;
  onNotificationClick?: () => void;
  collapsed: boolean;
  onToggleSidebar: () => void;
}

const TopBar: React.FC<TopBarProps> = ({ user, onNotificationClick, collapsed, onToggleSidebar }) => {
  const [showUserMenu, setShowUserMenu] = useState(false);
  const navigate = useNavigate();
  const menuRef = useRef<HTMLDivElement>(null);
  const { demoMode, toggleDemoMode } = useDemoMode();

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setShowUserMenu(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const handleSignOut = () => {
    // In a real app, this would handle logout logic
    navigate('/logout');
    setShowUserMenu(false);
  };

  return (
    <div className="fixed top-0 left-0 w-full z-50 bg-white border-b border-border h-16 flex items-center justify-between px-6 shadow-sm">
      {/* Left side */}
      <div className="flex items-center space-x-4">
        <button
          onClick={onToggleSidebar}
          className="p-1 rounded hover:bg-gray-100 transition-colors mr-2"
        >
          {collapsed ? <ChevronRight size={20} /> : <ChevronLeft size={20} />}
        </button>
        <Link to="/" className="flex items-center space-x-2">
          <div className="w-8 h-8 bg-moveos-primary rounded-lg flex items-center justify-center">
            <span className="text-white font-bold text-sm">M</span>
          </div>
          <span className="font-bold text-lg text-text-primary">Move OS</span>
        </Link>
      </div>

      {/* Right side */}
      <div className="flex items-center space-x-4">
        {/* Demo Mode Toggle */}
        <button
          onClick={toggleDemoMode}
          className={`flex items-center space-x-2 px-3 py-1 rounded-lg text-sm font-medium border transition-all duration-200 ${demoMode ? 'bg-blue-100 text-blue-800 border-blue-300' : 'bg-gray-100 text-gray-600 border-gray-300 hover:bg-gray-200'}`}
        >
          <Zap className="w-4 h-4" />
          <span>{demoMode ? 'Demo Mode ON' : 'Demo Mode'}</span>
        </button>
        {/* Notifications */}
        <Link to="/notifications" className="p-2 rounded-lg hover:bg-gray-100 transition-colors relative">
          <Bell size={20} className="text-text-secondary" />
          <div className="absolute -top-1 -right-1 w-3 h-3 bg-red-500 rounded-full"></div>
        </Link>

        {/* User Menu */}
        {user && (
          <div className="relative" ref={menuRef}>
            <button
              onClick={() => setShowUserMenu(!showUserMenu)}
              className="flex items-center space-x-2 p-2 rounded-lg hover:bg-gray-100 transition-colors"
            >
              <img
                src={user.profileImage?.startsWith('/images/') ? `${process.env.PUBLIC_URL}${user.profileImage}` : user.profileImage}
                alt={user.name}
                className="w-8 h-8 rounded-full object-cover"
              />
            </button>

            <AnimatePresence>
              {showUserMenu && (
                <motion.div
                  initial={{ opacity: 0, y: -10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -10 }}
                  className="absolute right-0 top-full mt-2 w-48 bg-white rounded-lg shadow-lg border border-border py-2 z-50"
                >
                  <div className="px-4 py-2 border-b border-border">
                    <p className="text-base font-medium text-text-primary">{user.name}</p>
                  </div>
                  <Link 
                    to="/profile" 
                    className="block w-full px-4 py-2 text-left text-[15px] text-text-secondary hover:bg-gray-50 flex items-center space-x-2"
                    onClick={() => setShowUserMenu(false)}
                  >
                    <User size={16} />
                    <span>Profile</span>
                  </Link>
                  <Link 
                    to="/settings" 
                    className="block w-full px-4 py-2 text-left text-[15px] text-text-secondary hover:bg-gray-50 flex items-center space-x-2"
                    onClick={() => setShowUserMenu(false)}
                  >
                    <Settings size={16} />
                    <span>Settings</span>
                  </Link>
                  <button 
                    onClick={handleSignOut}
                    className="w-full px-4 py-2 text-left text-[15px] text-red-600 hover:bg-gray-50 flex items-center space-x-2"
                  >
                    <LogOut size={16} />
                    <span>Sign Out</span>
                  </button>
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        )}
      </div>
    </div>
  );
};

export default TopBar; 