import React, { useState } from 'react';
import { Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Menu as MenuIcon } from 'react-feather';
import { UserProvider, useUser } from './contexts/UserContext';
import { PrototypeFlowProvider, usePrototypeFlow } from './contexts/PrototypeFlowContext';
import Home from './pages/Home';
import Dashboard from './pages/Dashboard';
import CityGuide from './pages/CityGuide';
import Services from './pages/Services';
import Settings from './pages/Settings';
import Profile from './pages/Profile';
import Notifications from './pages/Notifications';
import LogOut from './pages/LogOut';
import SignIn from './pages/SignIn';
import StartMove from './pages/StartMove';
import USPSForm from './pages/USPSForm';
import USPSConfirmation from './pages/USPSConfirmation';
import MoveOSSignup from './pages/MoveOSSignup';
import PrototypeDashboard from './pages/PrototypeDashboard';
import NotFound from './pages/NotFound';
import TopBar from './components/TopBar';
import Sidebar from './components/Sidebar';
import SupportChat from './components/SupportChat';
import './index.css';
import { DemoModeProvider } from './contexts/DemoModeContext';

function AppContent() {
  const { user, setUserById } = useUser();
  const { state, dispatch, startUSPSFlow, resetFlowData } = usePrototypeFlow();
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const [isFlowMenuOpen, setIsFlowMenuOpen] = useState(true);
  const [showSupportChat, setShowSupportChat] = useState(false);
  const navigate = useNavigate();

  const handleFlowSwitch = async (flow: 'sarah' | 'carlos' | 'usps') => {
    if (state.currentFlow === flow) return;
    
    if (flow === 'usps') {
      startUSPSFlow();
      return;
    }
    
    // Update the prototype flow state first
    dispatch({ type: 'SET_CURRENT_FLOW', payload: flow });
    localStorage.setItem('currentPrototypeFlow', flow);
    
    // Then switch users
    if (flow === 'sarah') {
      await setUserById('user-1');
      navigate('/dashboard');
    } else if (flow === 'carlos') {
      await setUserById('user-2');
      navigate('/dashboard');
    }
  };

  return (
    <>
      {/* Collapsible Prototype Flow Menu - Show on all pages except landing and signin */}
      {user && (
        <>
          {/* Floating button when collapsed */}
          {!isFlowMenuOpen && (
            <button
              className="fixed left-4 bottom-4 z-50 bg-moveos-primary text-white shadow-xl rounded-full p-4 flex items-center justify-center hover:bg-blue-700 transition-colors duration-200"
              onClick={() => setIsFlowMenuOpen(true)}
              aria-label="Open Prototype Flow Menu"
            >
              <MenuIcon className="w-6 h-6" />
            </button>
          )}
          {/* Expanded menu */}
          {isFlowMenuOpen && (
            <motion.div
              initial={{ x: -100, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              exit={{ x: -100, opacity: 0 }}
              transition={{ type: 'spring', stiffness: 300, damping: 30 }}
              className="fixed left-4 bottom-4 z-50 bg-white shadow-xl rounded-2xl p-4 flex flex-col items-center space-y-4 border border-border w-72"
            >
              <div className="flex items-center justify-between w-full mb-2">
                <h3 className="text-xs font-medium text-text-secondary uppercase tracking-wide">Prototype Flow</h3>
                <div className="flex items-center space-x-2">
                  <button
                    onClick={resetFlowData}
                    className="text-xs text-gray-400 hover:text-gray-600 transition-colors duration-200"
                    title="Reset all flow data"
                  >
                    Reset
                  </button>
                  <button
                    onClick={() => setIsFlowMenuOpen(false)}
                    className="text-xs text-gray-400 hover:text-gray-600 transition-colors duration-200"
                    title="Collapse menu"
                  >
                    <MenuIcon className="w-5 h-5" />
                  </button>
                </div>
              </div>
              {[
                {
                  id: 'carlos',
                  name: 'Carlos Rodriguez',
                  location: 'Miami  Denver',
                  status: 'Planning',
                  image: `${process.env.PUBLIC_URL}/images/carlos.png`,
                  description: 'Active planning experience',
                  isNew: false
                },
                {
                  id: 'sarah',
                  name: 'Sarah Chen',
                  location: 'Austin, TX',
                  status: 'Moved',
                  image: `${process.env.PUBLIC_URL}/images/sarah.png`,
                  description: 'Completed move experience',
                  isNew: false
                },
                {
                  id: 'usps',
                  name: 'USPS Integration',
                  location: 'Government Flow',
                  status: state.uspsProgress?.dashboardCompleted ? 'Complete' : 'Demo',
                  image: `${process.env.PUBLIC_URL}/images/usps-avatar.png`,
                  description: 'End-to-end USPS to Move OS transition',
                  isNew: false
                }
              ].map((flow) => (
                <motion.button
                  key={flow.id}
                  onClick={() => handleFlowSwitch(flow.id as 'sarah' | 'carlos' | 'usps')}
                  className={`w-full flex items-center space-x-3 p-3 rounded-xl transition-all duration-200 text-left relative group ${
                    state.currentFlow === flow.id
                      ? 'bg-moveos-primary text-white shadow-lg scale-105'
                      : 'hover:bg-gray-50 text-text-secondary hover:shadow-md'
                  }`}
                  whileHover={{ scale: state.currentFlow === flow.id ? 1.05 : 1.02 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <div className="relative">
                    <img
                      src={flow.image}
                      alt={flow.name}
                      className={`w-12 h-12 rounded-full object-cover border-2 ${
                        state.currentFlow === flow.id ? 'border-white' : 'border-gray-200'
                      }`}
                    />
                    {state.currentFlow === flow.id && (
                      <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 rounded-full border-2 border-white"></div>
                    )}
                  </div>
                  <div className="flex-1 min-w-0 flex flex-col space-y-0.5">
                    <div className="flex items-center space-x-2 min-w-0">
                      <div className="font-medium text-base truncate max-w-[180px]">{flow.name}</div>
                      {flow.id === 'usps' && state.uspsProgress?.dashboardCompleted && (
                        <span className="text-green-400"></span>
                      )}
                    </div>
                    <div className="text-xs opacity-75 truncate max-w-[180px]">{flow.location}</div>
                    <div className="text-xs opacity-75 truncate max-w-[180px]">{flow.description}</div>
                    <div className="mt-1">
                      <span className={`text-xs px-2 py-1 rounded-full ${
                        state.currentFlow === flow.id 
                          ? 'bg-white/20 text-white' 
                          : 'bg-gray-100 text-gray-600'
                      }`}>
                        {flow.status}
                      </span>
                    </div>
                  </div>
                </motion.button>
              ))}
              {/* Flow Status Indicator */}
              <div className="w-full pt-2 border-t border-gray-100">
                <div className="text-xs text-gray-500 text-center">
                  Current: {state.currentFlow === 'sarah' ? 'Sarah Chen' : state.currentFlow === 'carlos' ? 'Carlos Rodriguez' : 'USPS Integration'}
                </div>
                {state.uspsProgress && (
                  <div className="text-xs text-gray-400 text-center mt-1">
                    Step {state.uspsProgress.currentStep} of {state.uspsProgress.totalSteps}
                  </div>
                )}
              </div>
            </motion.div>
          )}
        </>
      )}

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/signin" element={<SignIn />} />
        <Route path="/start-move" element={user ? <StartMove /> : <Navigate to="/signin" replace />} />
        {/* USPS Flow Routes */}
        <Route path="/usps-form" element={<USPSForm />} />
        <Route path="/usps-confirmation" element={<USPSConfirmation />} />
        <Route path="/moveos-signup" element={<MoveOSSignup />} />
        <Route path="/prototype-dashboard" element={<PrototypeDashboard />} />
        <Route
          path="*"
          element={
            user ? (
              <div className="min-h-screen bg-neutral-bg">
                <TopBar
                  user={user}
                  collapsed={sidebarCollapsed}
                  onToggleSidebar={() => setSidebarCollapsed(!sidebarCollapsed)}
                />
                <div className="flex">
                  <Sidebar
                    collapsed={sidebarCollapsed}
                    onToggle={() => setSidebarCollapsed(!sidebarCollapsed)}
                    user={user}
                    onUserSwitch={setUserById}
                    currentUserId={user.id}
                  />
                  <div className={`flex-1 flex flex-col min-h-screen mt-16 ${sidebarCollapsed ? 'ml-16' : 'ml-64'}`}> 
                    <main className="flex-1 p-6">
                      <Routes>
                        <Route path="/dashboard" element={
                          state.currentFlow === 'usps' ? <PrototypeDashboard /> : <Dashboard />
                        } />
                        <Route path="/services" element={<Services />} />
                        <Route path="/city-guide" element={<CityGuide />} />
                        <Route path="/profile" element={<Profile />} />
                        <Route path="/settings" element={<Settings />} />
                        <Route path="/notifications" element={<Notifications />} />
                        <Route path="/logout" element={<LogOut />} />
                        <Route path="*" element={<NotFound />} />
                      </Routes>
                    </main>
                  </div>
                </div>
                {/* Support Chat */}
                <SupportChat
                  isOpen={showSupportChat}
                  onToggle={() => setShowSupportChat(!showSupportChat)}
                  user={user}
                />
              </div>
            ) : (
              <Navigate to="/" replace />
            )
          }
        />
      </Routes>
    </>
  );
}

function App() {
  return (
    <DemoModeProvider>
      <UserProvider>
        <PrototypeFlowProvider>
          <AppContent />
        </PrototypeFlowProvider>
      </UserProvider>
    </DemoModeProvider>
  );
}

export default App; 