import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { CheckCircle, Circle, Clock, AlertCircle, ChevronDown, ChevronRight } from 'lucide-react';
import { Task } from '../types';
import { getPriorityColor, getStatusColor, formatDate } from '../utils/data';

interface TaskListProps {
  tasks: Task[];
  onTaskUpdate: (taskId: string, status: Task['status']) => void;
  noCard?: boolean;
}

const TaskList: React.FC<TaskListProps> = ({ tasks, onTaskUpdate, noCard = false }) => {
  const [expandedTask, setExpandedTask] = useState<string | null>(null);

  const getStatusIcon = (status: Task['status']) => {
    switch (status) {
      case 'completed':
        return <CheckCircle size={20} className="text-success" />;
      case 'in-progress':
        return <Clock size={20} className="text-moveos-primary" />;
      case 'scheduled':
        return <Clock size={20} className="text-orange-500" />;
      case 'not-started':
        return <Circle size={20} className="text-gray-400" />;
      default:
        return <Circle size={20} className="text-gray-400" />;
    }
  };

  const getPriorityIcon = (priority: Task['priority']) => {
    switch (priority) {
      case 'high':
        return <AlertCircle size={16} className="text-red-500" />;
      case 'medium':
        return <AlertCircle size={16} className="text-orange-500" />;
      case 'low':
        return <AlertCircle size={16} className="text-green-500" />;
      default:
        return null;
    }
  };

  const handleTaskClick = (taskId: string) => {
    setExpandedTask(expandedTask === taskId ? null : taskId);
  };

  const handleStatusToggle = (task: Task) => {
    const newStatus = task.status === 'completed' ? 'in-progress' : 'completed';
    onTaskUpdate(task.id, newStatus);
  };

  const content = (
    <>
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-xl font-semibold text-text-primary">Tasks</h2>
        <span className="text-sm text-text-secondary">
          {tasks.filter(t => t.status === 'completed').length} of {tasks.length} completed
        </span>
      </div>
      
      <div className="space-y-2">
        {tasks.map((task, index) => (
          <motion.div
            key={task.id}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="border border-border rounded-lg p-4 hover:bg-gray-50 transition-colors"
          >
            <div className="flex items-start space-x-3">
              <button
                onClick={() => handleStatusToggle(task)}
                className="flex-shrink-0 mt-0.5"
              >
                {getStatusIcon(task.status)}
              </button>
              
              <div className="flex-1 min-w-0">
                <div className="flex items-center justify-between">
                  <h3 className="font-medium text-text-primary truncate">
                    {task.title}
                  </h3>
                  <div className="flex items-center space-x-2">
                    {getPriorityIcon(task.priority)}
                    <button
                      onClick={() => handleTaskClick(task.id)}
                      className="p-1 hover:bg-gray-200 rounded"
                    >
                      {expandedTask === task.id ? (
                        <ChevronDown size={16} />
                      ) : (
                        <ChevronRight size={16} />
                      )}
                    </button>
                  </div>
                </div>
                
                <p className="text-sm text-text-secondary mt-1">
                  {task.description}
                </p>
                
                <div className="flex items-center space-x-4 mt-2 text-xs text-text-secondary">
                  <span>Due: {formatDate(task.dueDate)}</span>
                  <span>Est: {task.estimatedTime}</span>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium`}
                        style={{ 
                          backgroundColor: `${getStatusColor(task.status)}20`,
                          color: getStatusColor(task.status)
                        }}>
                    {task.status.replace('-', ' ')}
                  </span>
                </div>
              </div>
            </div>
            
            {/* Expanded Details */}
            {expandedTask === task.id && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: 'auto' }}
                exit={{ opacity: 0, height: 0 }}
                className="mt-4 pt-4 border-t border-border"
              >
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <p className="font-medium text-text-primary">Category</p>
                    <p className="text-text-secondary capitalize">{task.category}</p>
                  </div>
                  <div>
                    <p className="font-medium text-text-primary">Priority</p>
                    <p className="text-text-secondary capitalize">{task.priority}</p>
                  </div>
                  {task.completedDate && (
                    <div>
                      <p className="font-medium text-text-primary">Completed</p>
                      <p className="text-text-secondary">{formatDate(task.completedDate)}</p>
                    </div>
                  )}
                  {task.actualTime && (
                    <div>
                      <p className="font-medium text-text-primary">Actual Time</p>
                      <p className="text-text-secondary">{task.actualTime}</p>
                    </div>
                  )}
                </div>
              </motion.div>
            )}
          </motion.div>
        ))}
      </div>
    </>
  );

  return noCard ? content : <div className="card">{content}</div>;
};

export default TaskList; 