import React, { useEffect, useRef } from 'react';
import { createPortal } from 'react-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { CheckCircle, Lock, X } from 'react-feather';

interface USPSCompletionModalProps {
  isOpen: boolean;
  onClose: () => void;
  onContinue: () => void;
}

const modalRoot = typeof window !== 'undefined' ? document.body : null;

const USPSCompletionModal: React.FC<USPSCompletionModalProps> = ({ isOpen, onClose, onContinue }) => {
  const modalRef = useRef<HTMLDivElement>(null);
  const firstFocusableRef = useRef<HTMLButtonElement>(null);

  // Focus trap and ESC key
  useEffect(() => {
    if (!isOpen) return;
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.key === 'Escape') onClose();
      // Trap focus
      if (e.key === 'Tab' && modalRef.current) {
        const focusableEls = modalRef.current.querySelectorAll<HTMLElement>(
          'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
        );
        const firstEl = focusableEls[0];
        const lastEl = focusableEls[focusableEls.length - 1];
        if (!e.shiftKey && document.activeElement === lastEl) {
          e.preventDefault();
          firstEl.focus();
        } else if (e.shiftKey && document.activeElement === firstEl) {
          e.preventDefault();
          lastEl.focus();
        }
      }
    };
    document.addEventListener('keydown', handleKeyDown);
    setTimeout(() => firstFocusableRef.current?.focus(), 0);
    return () => document.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, onClose]);

  // Prevent background scroll
  useEffect(() => {
    if (isOpen) document.body.style.overflow = 'hidden';
    else document.body.style.overflow = '';
    return () => { document.body.style.overflow = ''; };
  }, [isOpen]);

  if (!isOpen || !modalRoot) return null;

  return createPortal(
    <AnimatePresence>
      <motion.div
        key="usps-modal-backdrop"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 z-50 flex items-center justify-center bg-black/50"
        aria-modal="true"
        role="dialog"
        tabIndex={-1}
        onClick={onClose}
      >
        <motion.div
          key="usps-modal"
          initial={{ opacity: 0, scale: 0.96, y: 32 }}
          animate={{ opacity: 1, scale: 1, y: 0 }}
          exit={{ opacity: 0, scale: 0.96, y: 32 }}
          transition={{ duration: 0.22 }}
          className="relative bg-white rounded-2xl shadow-2xl max-w-lg w-full mx-4 p-0"
          style={{ outline: 'none' }}
          ref={modalRef}
          aria-labelledby="usps-modal-title"
          aria-describedby="usps-modal-desc"
          onClick={e => e.stopPropagation()}
        >
          {/* Dismiss button */}
          <button
            onClick={onClose}
            className="absolute top-5 right-5 text-gray-400 hover:text-gray-700 focus:outline-none focus:ring-2 focus:ring-moveos-primary rounded-full"
            aria-label="Close modal"
            ref={firstFocusableRef}
          >
            <X className="w-6 h-6" />
          </button>

          {/* Header Section */}
          <div className="flex flex-col items-center pt-12 pb-8 px-8">
            <CheckCircle className="w-16 h-16 text-green-500 mb-6" />
            <h2 id="usps-modal-title" className="text-3xl font-extrabold text-moveos-primary mb-3 text-center">
              Welcome to Move OS, Carlos!
            </h2>
            <p className="text-lg text-gray-700 text-center mb-4">
              Your USPS change of address is complete.
            </p>
            <div className="flex items-center justify-center text-gray-500 text-sm mb-2 mt-2">
              <Lock className="w-4 h-4 mr-2" />
              <span>Your information was securely transferred.</span>
            </div>
          </div>

          {/* Action Section */}
          <div className="px-8 pb-12 flex flex-col items-center">
            <button
              onClick={onContinue}
              className="w-full max-w-xs bg-moveos-primary text-white py-3 px-6 rounded-lg font-semibold text-base shadow hover:bg-blue-700 transition-colors duration-200 active:scale-95 focus:outline-none focus:ring-2 focus:ring-moveos-primary"
            >
              Continue to Dashboard
            </button>
          </div>
        </motion.div>
      </motion.div>
    </AnimatePresence>,
    modalRoot
  );
};

export default USPSCompletionModal; 