import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, Wifi, Zap, Shield, Truck, Check, Info } from 'react-feather';

interface ServiceOption {
  id: string;
  title: string;
  description: string;
  whyRecommend: string;
  benefit: string;
  provider: string;
  category: string;
}

interface ServicePreferencesModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSave: (preferences: string[]) => void;
  onSkip: () => void;
}

const serviceOptions: ServiceOption[] = [
  {
    id: 'internet',
    title: 'Internet Setup Assistance',
    description: 'Get help finding and setting up internet service at your new address',
    whyRecommend: 'Based on your move date and new location, we can help you find the best internet deals and schedule installation.',
    benefit: 'Save $15-30/month on internet service',
    provider: 'Allconnect',
    category: 'utilities'
  },
  {
    id: 'energy',
    title: 'Energy Provider Comparison',
    description: 'Compare energy rates and find the best provider for your new home',
    whyRecommend: 'Energy rates vary significantly by location. We can help you find competitive rates in your new area.',
    benefit: 'Save $20-40/month on energy bills',
    provider: 'Choose Energy',
    category: 'utilities'
  },
  {
    id: 'security',
    title: 'Home Security Consultation',
    description: 'Get personalized security recommendations for your new home',
    whyRecommend: 'New homes often need security assessments. We can connect you with trusted security providers.',
    benefit: 'Get 3 months free monitoring + equipment discounts',
    provider: 'SimpliSafe',
    category: 'security'
  },
  {
    id: 'moving',
    title: 'Moving Truck Rental Deals',
    description: 'Access exclusive rates on moving truck rentals and moving supplies',
    whyRecommend: 'Based on your move distance and timing, we can find the best rental rates and availability.',
    benefit: 'Save $50-150 on truck rental + free moving supplies',
    provider: 'U-Haul',
    category: 'moving'
  }
];

const ServicePreferencesModal: React.FC<ServicePreferencesModalProps> = ({
  isOpen,
  onClose,
  onSave,
  onSkip
}) => {
  const [selectedServices, setSelectedServices] = useState<string[]>([]);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Focus management
  useEffect(() => {
    if (isOpen) {
      const modalElement = document.getElementById('service-preferences-modal');
      if (modalElement) {
        modalElement.focus();
      }
    }
  }, [isOpen]);

  // Escape key handler
  useEffect(() => {
    const handleEscape = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && isOpen) {
        onClose();
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEscape);
      document.body.style.overflow = 'hidden';
    }

    return () => {
      document.removeEventListener('keydown', handleEscape);
      document.body.style.overflow = 'unset';
    };
  }, [isOpen, onClose]);

  const handleServiceToggle = (serviceId: string) => {
    setSelectedServices(prev => 
      prev.includes(serviceId) 
        ? prev.filter(id => id !== serviceId)
        : [...prev, serviceId]
    );
  };

  const handleSave = async () => {
    setIsSubmitting(true);
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000));
    onSave(selectedServices);
    setIsSubmitting(false);
  };

  const handleSkip = () => {
    onSkip();
    onClose();
  };

  const handleDecideLater = () => {
    onClose();
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          {/* Backdrop */}
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black bg-opacity-50"
            onClick={onClose}
          />

          {/* Modal */}
          <div className="flex items-center justify-center min-h-screen p-4">
            <motion.div
              id="service-preferences-modal"
              initial={{ opacity: 0, scale: 0.95, y: 20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.95, y: 20 }}
              transition={{ duration: 0.2 }}
              className="relative bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-hidden"
              role="dialog"
              aria-modal="true"
              aria-labelledby="modal-title"
              aria-describedby="modal-description"
            >
              {/* Header */}
              <div className="flex items-center justify-between p-6 border-b border-gray-200">
                <div>
                  <h2 id="modal-title" className="text-xl font-bold text-text-primary">
                    Customize Your Service Recommendations
                  </h2>
                  <p id="modal-description" className="text-sm text-text-secondary mt-1">
                    All services are optional. Select what interests you.
                  </p>
                </div>
                <button
                  onClick={onClose}
                  className="p-2 text-gray-400 hover:text-gray-600 transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-moveos-primary focus:ring-offset-2 rounded-lg"
                  aria-label="Close modal"
                >
                  <X className="w-5 h-5" />
                </button>
              </div>

              {/* Content */}
              <div className="p-6 overflow-y-auto max-h-[60vh]">
                <div className="space-y-4">
                  {serviceOptions.map((service) => (
                    <motion.div
                      key={service.id}
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      className="border border-gray-200 rounded-lg p-4 hover:border-gray-300 transition-colors duration-200"
                    >
                      <div className="flex items-start space-x-3">
                        <input
                          type="checkbox"
                          id={service.id}
                          checked={selectedServices.includes(service.id)}
                          onChange={() => handleServiceToggle(service.id)}
                          className="mt-1 w-4 h-4 text-moveos-primary rounded focus:ring-moveos-primary border-gray-300"
                        />
                        <div className="flex-1">
                          <div className="flex items-center space-x-2 mb-2">
                            {service.id === 'internet' && <Wifi className="w-4 h-4 text-blue-600" />}
                            {service.id === 'energy' && <Zap className="w-4 h-4 text-yellow-600" />}
                            {service.id === 'security' && <Shield className="w-4 h-4 text-green-600" />}
                            {service.id === 'moving' && <Truck className="w-4 h-4 text-orange-600" />}
                            <label htmlFor={service.id} className="font-semibold text-text-primary cursor-pointer">
                              {service.title}
                            </label>
                          </div>
                          
                          <p className="text-sm text-text-secondary mb-3">
                            {service.description}
                          </p>

                          {/* Why We Recommend */}
                          <div className="bg-blue-50 rounded-lg p-3 mb-3">
                            <div className="flex items-start space-x-2">
                              <Info className="w-4 h-4 text-blue-600 mt-0.5 flex-shrink-0" />
                              <div>
                                <p className="text-sm font-medium text-blue-800 mb-1">Why we recommend this:</p>
                                <p className="text-xs text-blue-700">{service.whyRecommend}</p>
                              </div>
                            </div>
                          </div>

                          {/* Benefit and Provider */}
                          <div className="flex justify-between items-center">
                            <div className="text-sm">
                              <span className="text-green-600 font-medium">{service.benefit}</span>
                              <br />
                              <span className="text-text-secondary">
                                Powered by <span className="font-medium text-moveos-primary">{service.provider}</span>
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </motion.div>
                  ))}
                </div>
              </div>

              {/* Footer */}
              <div className="p-6 border-t border-gray-200 bg-gray-50">
                {/* Compliance Notice */}
                <div className="mb-4 p-3 bg-white border border-gray-200 rounded-lg">
                  <p className="text-xs text-gray-600 text-center">
                    You can change these anytime. We never share your information without explicit consent.
                  </p>
                </div>

                {/* Action Buttons */}
                <div className="flex flex-col sm:flex-row gap-3">
                  <button
                    onClick={handleSave}
                    disabled={isSubmitting}
                    className="flex-1 btn-primary py-3 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {isSubmitting ? (
                      <div className="flex items-center justify-center">
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                        Saving...
                      </div>
                    ) : (
                      'Save Preferences'
                    )}
                  </button>
                  
                  <button
                    onClick={handleSkip}
                    className="flex-1 py-3 px-4 border border-gray-300 rounded-lg font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-moveos-primary transition-colors duration-200"
                  >
                    Skip All Services
                  </button>
                  
                  <button
                    onClick={handleDecideLater}
                    className="flex-1 py-3 px-4 text-gray-500 hover:text-gray-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-moveos-primary transition-colors duration-200"
                  >
                    Decide Later
                  </button>
                </div>
              </div>
            </motion.div>
          </div>
        </div>
      )}
    </AnimatePresence>
  );
};

export default ServicePreferencesModal; 