import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { motion } from 'framer-motion';
import { 
  Home, 
  Plus, 
  Wifi, 
  MapPin, 
  User,
  ChevronLeft,
  ChevronRight
} from 'react-feather';
import { User as UserType } from '../types';

interface SidebarProps {
  collapsed: boolean;
  onToggle: () => void;
  user: UserType | null;
  onUserSwitch?: (userId: string) => void;
  currentUserId?: string;
  className?: string;
}

const Sidebar: React.FC<SidebarProps> = ({ 
  collapsed, 
  onToggle, 
  user, 
  onUserSwitch,
  currentUserId = 'user-1',
  className = ''
}) => {
  const location = useLocation();

  const menuItems = [
    { path: '/dashboard', label: 'Home', icon: Home },
    { path: '/start-move', label: 'Start New Move', icon: Plus },
    { path: '/services', label: 'Services', icon: Wifi, protected: true },
    { path: '/city-guide', label: 'City Guide', icon: MapPin, protected: true },
  ];

  const flowOptions = [
    {
      id: 'user-1',
      name: 'Sarah Chen',
      location: 'Austin, TX',
      status: 'Moved',
      image: `${process.env.PUBLIC_URL}/images/sarah.png`
    },
    {
      id: 'user-2', 
      name: 'Carlos Rodriguez',
      location: 'Miami  Denver',
      status: 'Planning',
      image: `${process.env.PUBLIC_URL}/images/carlos.png`
    }
  ];

  const currentFlow = flowOptions.find(flow => flow.id === currentUserId) || flowOptions[0];

  const sidebarVariants = {
    expanded: { width: 220 },
    collapsed: { width: 64 }
  };

  return (
    <motion.div
      className={`fixed top-16 left-0 z-40 bg-white border-r border-border h-[calc(100vh-4rem)] flex flex-col ${
        collapsed ? 'w-16' : 'w-64'
      } ${className}`}
      variants={sidebarVariants}
      animate={collapsed ? 'collapsed' : 'expanded'}
      transition={{ duration: 0.3, ease: 'easeInOut' }}
    >
      <div className="relative h-full flex flex-col">
        {/* Navigation */}
        <nav className="flex-1 py-4">
          {menuItems.map((item) => {
            const Icon = item.icon;
            const isActive = location.pathname === item.path;
            const isProtected = item.protected && !user;

            return (
              <Link
                key={item.path}
                to={isProtected ? '/' : item.path}
                className={`sidebar-item ${isActive ? 'active' : ''} ${
                  isProtected ? 'opacity-50 cursor-not-allowed' : ''
                }`}
              >
                <Icon size={20} className="flex-shrink-0" />
                {!collapsed && (
                  <motion.span
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    className="ml-3"
                  >
                    {item.label}
                  </motion.span>
                )}
              </Link>
            );
          })}
        </nav>
      </div>
    </motion.div>
  );
};

export default Sidebar; 